import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';

type KycForm = {
  full_name: string;
  dob: string;
  country: string;
  id_number: string;
  doc_front: File | null;
  doc_back: File | null;
  selfie: File | null;
};

const ProfilePage = () => {
  const { user, isLoading, updateProfile, changePassword } = useAuth();
  const [activeTab, setActiveTab] = useState('profile');
  const [kycStatus, setKycStatus] = useState<string>('unsubmitted');
  const [kycForm, setKycForm] = useState<KycForm>({ full_name: '', dob: '', country: '', id_number: '', doc_front: null, doc_back: null, selfie: null });
  const [kycSubmitting, setKycSubmitting] = useState<boolean>(false);
  const [previews, setPreviews] = React.useState<Record<string, string | undefined>>({});

  const [profileForm, setProfileForm] = useState({
    username: '',
    email: '',
    phone: '',
    country: '',
    city: '',
    address: '',
  });

  const [passwordForm, setPasswordForm] = useState({
    current_password: '',
    new_password: '',
    confirm_password: '',
  });

  const [saving, setSaving] = useState(false);
  const [message, setMessage] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  // Two-factor auth (frontend placeholder supporting future backend integration)
  const [twofaEnabled, setTwofaEnabled] = useState<boolean>(false);
  const [twofaSetup, setTwofaSetup] = useState<boolean>(false); // in-progress setup state (shows QR + secret)
  const [twofaSecret, setTwofaSecret] = useState<string>('');
  const [twofaQr, setTwofaQr] = useState<string>('');
  const [twofaCode, setTwofaCode] = useState<string>('');
  const [twofaBusy, setTwofaBusy] = useState<boolean>(false);

  useEffect(() => {
    if (user) {
      setProfileForm({
        username: user.username || '',
        email: user.email || '',
        phone: user.phone || '',
        country: user.country || '',
        city: user.city || '',
        address: user.address || '',
      });
      // populate 2FA enabled flag from user when available (guard for typings)
      if (user && Object.prototype.hasOwnProperty.call(user, 'twofa_enabled')) {
        setTwofaEnabled(Boolean((user as any).twofa_enabled));
      }
    }
  }, [user]);

  const handleProfileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setProfileForm(prev => ({ ...prev, [name]: value }));
  };

  const submitProfile = async () => {
    try {
      setSaving(true);
      setMessage(null);
      setError(null);
      await updateProfile(profileForm);
      setMessage(' ');
    } catch (e) {
      setError(e instanceof Error ? e.message : '  ');
    } finally {
      setSaving(false);
    }
  };

  const submitPassword = async () => {
    if (passwordForm.new_password !== passwordForm.confirm_password) {
      setError('  ');
      return;
    }
    try {
      setSaving(true);
      setMessage(null);
      setError(null);
      await changePassword(passwordForm.current_password, passwordForm.new_password);
      setMessage(' ');
      setPasswordForm({ current_password: '', new_password: '', confirm_password: '' });
    } catch (e) {
      setError(e instanceof Error ? e.message : '  ');
    } finally {
      setSaving(false);
    }
  };

  // 2FA helpers (assumptions: backend endpoints exist at /api/2fa/setup, /api/2fa/verify, /api/2fa/disable)
  const startTwoFaSetup = async () => {
    try {
      setTwofaBusy(true);
      setMessage(null); setError(null);
      const token = localStorage.getItem('auth_token') || '';
  const res = await fetch((process.env.REACT_APP_API_URL || '') + '/api/2fa/setup', { method: 'POST', headers: { 'Authorization': `Bearer ${token}` } });
      if (!res.ok) throw new Error(`HTTP ${res.status}`);
      const j = await res.json();
      // expected: { secret: 'ABC...', qr: 'data:image/png;base64,...' }
      setTwofaSecret(j.secret || '');
      setTwofaQr(j.qr || '');
      setTwofaSetup(true);
    } catch (e:any) {
      setError(e?.message || '   2FA');
    } finally { setTwofaBusy(false); }
  };

  const verifyTwoFa = async () => {
    try {
      setTwofaBusy(true);
      setMessage(null); setError(null);
      const token = localStorage.getItem('auth_token') || '';
  const res = await fetch((process.env.REACT_APP_API_URL || '') + '/api/2fa/verify', { method: 'POST', headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' }, body: JSON.stringify({ code: twofaCode }) });
      const j = await res.json().catch(() => ({}));
      if (!res.ok || !j.ok) throw new Error(j.detail || '    ');
      setTwofaEnabled(true);
      setTwofaSetup(false);
      setTwofaSecret(''); setTwofaQr(''); setTwofaCode('');
      setMessage('  ');
    } catch (e:any) { setError(e?.message || '  2FA'); }
    finally { setTwofaBusy(false); }
  };

  const disableTwoFa = async () => {
    try {
      setTwofaBusy(true); setMessage(null); setError(null);
      const token = localStorage.getItem('auth_token') || '';
  const res = await fetch((process.env.REACT_APP_API_URL || '') + '/api/2fa/disable', { method: 'POST', headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' }, body: JSON.stringify({}) });
      if (!res.ok) throw new Error(`HTTP ${res.status}`);
      setTwofaEnabled(false);
      setMessage('2FA ');
    } catch (e:any) { setError(e?.message || '   2FA'); }
    finally { setTwofaBusy(false); }
  };

  return (
    <div className="profile-page page-bg" style={{ padding: '28px 0' }}>
      <div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
        <div style={{ width: '100%', maxWidth: 1200, padding: '0 20px', boxSizing: 'border-box' }}>
          <div className="profile-header" style={{ textAlign: 'left', marginBottom: 18 }}>
            <h1 className="page-title"></h1>
            <p className="page-subtitle"> ,   </p>
          </div>

          {(message || error) && (
            <div className={`notice ${error ? 'error' : 'success'}`} style={{ marginBottom: 16 }}>
              {error || message}
            </div>
          )}

          <div className="profile-shell">
            <aside className="profile-sidebar profile-card">
              <div className="avatar-block">
                <div className="avatar-placeholder">{user?.username ? user.username[0].toUpperCase() : '?'}</div>
                <div className="profile-name" style={{ marginTop: 12 }}>{user?.username || 'User'}</div>
                <div className="profile-email" style={{ fontSize: 13, opacity: 0.85 }}>{user?.email || ''}</div>
              </div>

              <nav className="profile-nav compact">
                <button className={`nav-tab ${activeTab === 'profile' ? 'active' : ''}`} onClick={() => setActiveTab('profile')}> </button>
                <button className={`nav-tab ${activeTab === 'kyc' ? 'active' : ''}`} onClick={() => setActiveTab('kyc')}>KYC</button>
                <button className={`nav-tab ${activeTab === 'security' ? 'active' : ''}`} onClick={() => setActiveTab('security')}></button>
              </nav>

              <div style={{ marginTop: 18 }}>
                <div style={{ fontSize: 13, color: 'var(--muted)' }}></div>
                <div style={{ fontWeight: 700 }}>{user?.created_at ? new Date(user.created_at).toLocaleDateString() : '-'}</div>
              </div>
            </aside>

            <main className="profile-main">
              <section className="profile-card">
                {activeTab === 'profile' && (
                  <div className={`tab-content ${activeTab}-tab`}> 
                    <div className="profile-controls" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                      <h2> </h2>
                      <div style={{ fontSize: 13, color: 'var(--muted)' }}>ID: <b>{user?.id || '-'}</b></div>
                    </div>
                    <div className="form-grid">
                      <div>
                        <label className="form-label"> </label>
                        <input name="username" value={profileForm.username} onChange={handleProfileChange} className="form-input" />
                      </div>
                      <div>
                        <label className="form-label">Email</label>
                        <input name="email" type="email" value={profileForm.email} onChange={handleProfileChange} className="form-input" />
                      </div>
                      <div>
                        <label className="form-label"></label>
                        <input name="phone" value={profileForm.phone} onChange={handleProfileChange} className="form-input" />
                      </div>
                      <div>
                        <label className="form-label"></label>
                        <input name="country" value={profileForm.country} onChange={handleProfileChange} className="form-input" />
                      </div>
                      <div>
                        <label className="form-label"></label>
                        <input name="city" value={profileForm.city} onChange={handleProfileChange} className="form-input" />
                      </div>
                      <div style={{ gridColumn: '1 / -1' }}>
                        <label className="form-label"></label>
                        <input name="address" value={profileForm.address} onChange={handleProfileChange} className="form-input" />
                      </div>
                    </div>
                    <div style={{ marginTop: 12, display: 'flex', gap: 10 }}>
                      <button disabled={saving || isLoading} className="btn btn-primary" onClick={submitProfile}>{saving ? '...' : ''}</button>
                      <button className="btn btn-secondary" onClick={() => { setProfileForm({ username: user?.username || '', email: user?.email || '', phone: user?.phone || '', country: user?.country || '', city: user?.city || '', address: user?.address || '' }); setMessage(null); setError(null); }}></button>
                    </div>
                  </div>
                )}

                {activeTab === 'kyc' && (
                  <div className={`tab-content ${activeTab}-tab`}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                      <h2> (KYC)</h2>
                      <div style={{ fontSize: 13, color: 'var(--muted)' }}>: <b>{kycStatus}</b></div>
                    </div>
                    {kycStatus !== 'approved' && (
                      <div className="kyc-notice" style={{ marginBottom: 12 }}>
                              KYC.     .
                      </div>
                    )}
                    <div style={{ display: 'grid', gap: 8 }}>
                      <input placeholder="" value={kycForm.full_name} onChange={e => setKycForm({ ...kycForm, full_name: e.target.value })} className="form-input" />
                      <input placeholder="  (YYYY-MM-DD)" value={kycForm.dob} onChange={e => setKycForm({ ...kycForm, dob: e.target.value })} className="form-input" />
                      <input placeholder="" value={kycForm.country} onChange={e => setKycForm({ ...kycForm, country: e.target.value })} className="form-input" />
                      <input placeholder=" " value={kycForm.id_number} onChange={e => setKycForm({ ...kycForm, id_number: e.target.value })} className="form-input" />
                      <div className="kyc-cards">
                        {(['doc_front','doc_back','selfie'] as Array<keyof typeof kycForm>).map((field) => (
                          <div key={field} className="kyc-card">
                            <div style={{ fontWeight: 700, marginBottom: 8 }}>{field === 'doc_front' ? ' ()' : field === 'doc_back' ? ' ()' : '  '}</div>
                            <div style={{ marginBottom: 8 }}>
                              {previews[field] ? <img src={previews[field]} alt={String(field)} style={{ maxWidth: '100%', borderRadius: 8 }} /> : <div style={{ height: 90, display: 'flex', alignItems: 'center', justifyContent: 'center', color: 'var(--muted)' }}>   </div>}
                            </div>
                            <div>
                              <input type="file" accept="image/*" onChange={e => {
                                const file = e.target.files ? e.target.files[0] : null;
                                setKycForm(prev => ({ ...prev, [field]: file } as any));
                                if (file) {
                                  const reader = new FileReader();
                                  reader.onload = ev => setPreviews(p => ({ ...p, [field]: String(ev.target?.result) }));
                                  reader.readAsDataURL(file);
                                } else {
                                  setPreviews(p => ({ ...p, [field]: undefined }));
                                }
                              }} className="file-input" />
                            </div>
                            <div style={{ marginTop: 8, fontSize: 12, color: 'var(--muted)' }}>JPG/PNG,  8 </div>
                          </div>
                        ))}
                      </div>
                      <div style={{ display: 'flex', gap: 8 }}>
                        <button disabled={kycSubmitting} className="btn btn-primary" onClick={async () => {
                          try {
                            setKycSubmitting(true);
                            setMessage(null); setError(null);
                            const fd = new FormData();
                            fd.append('full_name', kycForm.full_name || '');
                            fd.append('dob', kycForm.dob || '');
                            fd.append('country', kycForm.country || '');
                            fd.append('id_number', kycForm.id_number || '');
                            if (kycForm.doc_front) fd.append('doc_front', kycForm.doc_front);
                            if (kycForm.doc_back) fd.append('doc_back', kycForm.doc_back);
                            if (kycForm.selfie) fd.append('selfie', kycForm.selfie);
                            const token = localStorage.getItem('auth_token') || '';
                            const res = await fetch((process.env.REACT_APP_API_URL || '') + '/api/kyc/submit', { method: 'POST', headers: { 'Authorization': `Bearer ${token}` }, body: fd });
                            if (!res.ok) { const j = await res.json().catch(() => ({})); throw new Error(j.detail || `HTTP ${res.status}`); }
                            setMessage('KYC   ');
                            setKycStatus('pending');
                          } catch (e:any) { setError(e?.message || '  KYC'); }
                          finally { setKycSubmitting(false); }
                        }}>  </button>
                        <button className="btn btn-secondary" onClick={async () => {
                          try {
                            const token = localStorage.getItem('auth_token') || '';
                            const res = await fetch((process.env.REACT_APP_API_URL || '') + '/api/kyc/status', { headers: { 'Authorization': `Bearer ${token}` } });
                            const j = await res.json();
                            setKycStatus(j.kyc_status || 'unsubmitted');
                            setMessage(' ');
                          } catch (e:any) { setError(e?.message || ''); }
                        }}> </button>
                      </div>
                    </div>
                  </div>
                )}

                {activeTab === 'security' && (
                  <div className={`tab-content ${activeTab}-tab`}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                      <h2></h2>
                      <div style={{ fontSize: 13, color: 'var(--muted)' }}>2FA: <b>{twofaEnabled ? '' : ''}</b></div>
                    </div>

                    <div className="security-grid">
                      <div>
                        <label className="form-label"> </label>
                        <input type="password" placeholder=" " value={passwordForm.current_password} onChange={e => setPasswordForm({ ...passwordForm, current_password: e.target.value })} className="form-input" />
                        <input type="password" placeholder=" " value={passwordForm.new_password} onChange={e => setPasswordForm({ ...passwordForm, new_password: e.target.value })} className="form-input" style={{ marginTop: 8 }} />
                        <input type="password" placeholder="" value={passwordForm.confirm_password} onChange={e => setPasswordForm({ ...passwordForm, confirm_password: e.target.value })} className="form-input" style={{ marginTop: 8 }} />
                        <div style={{ marginTop: 10 }}>
                          <button disabled={saving || isLoading} className="btn btn-secondary" onClick={submitPassword}>{saving ? '...' : ' '}</button>
                        </div>
                      </div>

                      <div>
                        <label className="form-label">  (2FA)</label>
                        <div className="twofa-card">
                          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <div style={{ fontWeight: 700 }}>{twofaEnabled ? '' : ''}</div>
                            <div className="status-badge {twofaEnabled ? 'enabled' : 'disabled'}" style={{ fontSize: 12 }}>{twofaEnabled ? '' : ''}</div>
                          </div>
                          {!twofaEnabled && !twofaSetup && (
                            <div style={{ marginTop: 12 }}>
                              <div style={{ fontSize: 13, color: 'var(--muted)', marginBottom: 8 }}>      .</div>
                              <div style={{ display: 'flex', gap: 8 }}>
                                <button className="btn btn-primary" onClick={startTwoFaSetup} disabled={twofaBusy}> 2FA</button>
                              </div>
                            </div>
                          )}

                          {twofaSetup && (
                            <div style={{ marginTop: 12 }}>
                              <div style={{ display: 'flex', gap: 12, alignItems: 'center' }}>
                                <div className="qr-image" style={{ width: 120, height: 120, borderRadius: 8, background: '#f6f7fb', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                  {twofaQr ? <img src={twofaQr} alt="qr" style={{ width: '100%', height: '100%', objectFit: 'contain' }} /> : <div style={{ fontSize: 12, color: 'var(--muted)' }}>QR</div>}
                                </div>
                                <div style={{ flex: 1 }}>
                                  <div style={{ fontSize: 13, color: 'var(--muted)' }}>  (     )</div>
                                  <div style={{ marginTop: 6, padding: 8, background: 'rgba(0,0,0,0.04)', borderRadius: 8, fontFamily: 'monospace' }}>{twofaSecret || ''}</div>
                                  <div style={{ marginTop: 10, display: 'flex', gap: 8 }}>
                                    <input placeholder="  " value={twofaCode} onChange={e => setTwofaCode(e.target.value)} className="form-input" style={{ flex: 1 }} />
                                    <button className="btn btn-primary" onClick={verifyTwoFa} disabled={twofaBusy}></button>
                                  </div>
                                </div>
                              </div>
                              <div style={{ marginTop: 8, fontSize: 12, color: 'var(--muted)' }}>    2FA  .</div>
                            </div>
                          )}

                          {twofaEnabled && (
                            <div style={{ marginTop: 12 }}>
                              <div style={{ fontSize: 13, color: 'var(--muted)', marginBottom: 8 }}>      2FA,   .</div>
                              <div style={{ display: 'flex', gap: 8 }}>
                                <button className="btn btn-secondary" onClick={disableTwoFa} disabled={twofaBusy}> 2FA</button>
                              </div>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </section>
            </main>
          </div>
        </div>
      </div>
    </div>
  );
};


export default ProfilePage;