import React from 'react';
import { createHashRouter, RouterProvider, Navigate, Link, useLocation, Outlet } from 'react-router-dom';
import { ThemeProvider, useTheme } from './contexts/ThemeContext';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import { AutoTradeProvider } from './contexts/AutoTradeContext';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import TradePage from './pages/TradePage';
import ProfilePage from './pages/ProfilePage';
import WalletPage from './pages/WalletPage';
import LoginPage from './pages/LoginPage';
import RegisterPage from './pages/RegisterPage';
import AdminPage from './pages/AdminPage';
import BrokerPage from './pages/BrokerPage';
import ActivationPage from './pages/ActivationPage';
import ChatWidget from './components/ChatWidget';
import './App.css';

// Prefer an explicit build-time API URL, but default to same-origin so the
// frontend works when served behind nginx (proxying /api to the backend).
// If REACT_APP_API_URL is not set, API_BASE will be the empty string and
// `${API_BASE}/api/...` resolves to `/api/...` (same-origin).
const API_BASE = process.env.REACT_APP_API_URL || '';

const Navigation = () => {
  const location = useLocation();
  const { theme, toggleTheme } = useTheme();
  const { user, logout, isAdmin } = useAuth();
  const [menuOpen, setMenuOpen] = React.useState(false);
  const menuRef = React.useRef<HTMLDivElement | null>(null);
  const notificationsRef = React.useRef<HTMLDivElement | null>(null);
  const [isRefreshingBalance, setIsRefreshingBalance] = React.useState(false);
  const [notificationsOpen, setNotificationsOpen] = React.useState(false);
  const [notifications, setNotifications] = React.useState<any[]>([]);
  const [unreadCount, setUnreadCount] = React.useState(0);

  const fetchNotifications = React.useCallback(async () => {
    try {
      const token = localStorage.getItem('auth_token') || '';
      const res = await fetch(`${API_BASE}/api/notifications`, { headers: { Authorization: `Bearer ${token}` } });
      if (!res.ok) return;
      const arr = await res.json();
      setNotifications(arr || []);
      setUnreadCount((arr || []).filter((n: any) => !n.is_read).length);
    } catch (e) {
      // ignore
    }
  }, []);

  React.useEffect(() => {
    if (!user) return;
    fetchNotifications();
    const t = setInterval(fetchNotifications, 8000);
    const onBrokerNotification = (e: any) => { fetchNotifications(); };
    window.addEventListener('broker-notification', onBrokerNotification as EventListener);

  // open dedicated notifications websocket for real-time updates
  let ws: WebSocket | null = null;
      try {
      // WebSocket base can be overridden at build time with REACT_APP_WS_URL.
      // Otherwise use the current page origin and appropriate ws/wss proto.
      const proto = window.location.protocol === 'https:' ? 'wss' : 'ws';
      const wsBase = (process.env.REACT_APP_WS_URL && process.env.REACT_APP_WS_URL.length > 0)
        ? process.env.REACT_APP_WS_URL.replace(/\/+$/, '')
        : `${proto}://${window.location.host}`;
      const token = localStorage.getItem('auth_token') || '';
      const url = `${wsBase.replace(/\/+$/, '')}/ws/notifications?token=${encodeURIComponent(token)}`;
      ws = new WebSocket(url);
      ws.addEventListener('message', (ev) => {
        try {
          const obj = JSON.parse(ev.data);
          if (obj && obj.type === 'broker_notification') {
            fetchNotifications();
          }
        } catch (e) {}
      });
      ws.addEventListener('open', () => { try { ws?.send(JSON.stringify({ action: 'ping' })); } catch {} });
    } catch (e) {
      ws = null;
    }
    const onAppLogout = () => { try { ws?.close(); } catch {} };
    window.addEventListener('app-logout', onAppLogout as EventListener);
    return () => { clearInterval(t); window.removeEventListener('broker-notification', onBrokerNotification as EventListener); window.removeEventListener('app-logout', onAppLogout as EventListener); try { ws?.close(); } catch {} };
  }, [user, fetchNotifications]);

  const ackNotification = async (id: number) => {
    try {
      const token = localStorage.getItem('auth_token') || '';
      await fetch(`${API_BASE}/api/notifications/${id}/ack`, { method: 'POST', headers: { Authorization: `Bearer ${token}` } });
      fetchNotifications();
    } catch (e) {}
  };

  const shouldShowRefresh = location.pathname.startsWith('/trade') || location.pathname.startsWith('/wallet');

  const refreshProfile = async () => {
    window.location.reload();
  };

  React.useEffect(() => {
    const handler = () => { refreshProfile(); };
    window.addEventListener('profile-refresh-request', handler as EventListener);
    return () => window.removeEventListener('profile-refresh-request', handler as EventListener);
  }, [user]);

  const isActive = (path: string) => {
    if (path === '/') {
      return location.pathname === '/';
    }
    return location.pathname.startsWith(path);
  };

  const handleLogout = () => {
    logout();
    setMenuOpen(false);
  };

  React.useEffect(() => {
    const onClickOutside = (e: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(e.target as Node)) {
        setMenuOpen(false);
      }
      // close notifications if click outside notificationsRef and outside the bell button
      if (notificationsRef.current && !notificationsRef.current.contains(e.target as Node)) {
        setNotificationsOpen(false);
      }
    };
    document.addEventListener('mousedown', onClickOutside);
    return () => document.removeEventListener('mousedown', onClickOutside);
  }, []);

  if (location.pathname === '/login' || location.pathname === '/register') {
    return null;
  }

  return (
    <nav className={`main-nav ${theme}`}>
      <div className="nav-brand">
        <Link to="/" className="brand-link">
          <span className="brand-icon"></span>
          <span className="brand-text">Synergy Trade</span>
        </Link>
      </div>
      
      <div className="nav-links">
        <Link to="/" className={`nav-link ${isActive('/') ? 'active' : ''}`}>
          
        </Link>
        {user && (
          <>
            <Link to="/trade" className={`nav-link ${isActive('/trade') ? 'active' : ''}`}>
              
            </Link>
            {typeof user.broker_tier === 'string' && (user.broker_tier.toLowerCase().includes('') || user.broker_tier.toLowerCase().includes('broker')) && (
              <Link to="/broker" className={`nav-link ${isActive('/broker') ? 'active' : ''}`}>
                
              </Link>
            )}
          </>
        )}
        <Link to="/about" className={`nav-link ${isActive('/about') ? 'active' : ''}`}>
           
        </Link>
      </div>
      
      <div className="nav-user">
        <button 
          onClick={toggleTheme}
          className="theme-toggle"
          title={theme === 'light' ? '   ' : '   '}
        >
          {theme === 'light' ? '' : ''}
        </button>
        
        {user ? (
            <div className="user-menu" ref={menuRef}>
            <span className="user-balance-chip" style={{
              padding: '6px 10px',
              borderRadius: 8,
              fontWeight: 700,
              letterSpacing: '0.02em',
              background: theme === 'dark' ? '#2a3454' : '#e8f5e9',
              color: theme === 'dark' ? '#e0f2ff' : '#1b5e20',
              border: `1px solid ${theme === 'dark' ? '#3b4a78' : '#c8e6c9'}`,
            }}>
              ${user.balance.toFixed(2)}
            </span>
            <div style={{ position: 'relative', display: 'inline-block' }} ref={notificationsRef}>
                <button
                  className="balance-refresh-btn"
                  onClick={(e) => { e.stopPropagation(); setNotificationsOpen(o => !o); if (!notificationsOpen) fetchNotifications(); }}
                  title=""
                  aria-label=""
                >
                   {unreadCount > 0 ? <span style={{ color: '#fff', background: '#d32f2f', padding: '2px 6px', borderRadius: 12, marginLeft: 6, fontSize: 12 }}>{unreadCount}</span> : null}
                </button>
                <div className={`notifications-dropdown ${notificationsOpen ? 'open' : 'closed'}`} style={{ position: 'absolute', right: 0, top: 'calc(100% + 10px)', zIndex: 2000 }}>
                    <div style={{ padding: 8, fontWeight: 700, background: 'var(--card)', borderRadius: '8px 8px 0 0', borderBottom: '1px solid rgba(0,0,0,0.04)' }}></div>
                    <div className="notifications-list" style={{ padding: 12, background: 'var(--card)' }}>
                      {(notifications || []).length === 0 && <div style={{ padding: 12, background: 'var(--card)', borderRadius: 8 }}> </div>}
                      {(notifications || []).map(n => (
                        <div key={n.id} className="notification-card" style={{ background: n.is_read ? 'transparent' : 'linear-gradient(90deg, rgba(255,248,240,0.6), rgba(255,255,255,0.04))' }}>
                          <div className="meta">{n.created_at ? new Date(n.created_at).toLocaleString() : ''}</div>
                          <div className="body">
                            <div style={{ fontWeight: 700 }}>{n.type || ''}</div>
                            <div style={{ fontSize: 13, color: 'var(--muted)' }}>{n.message || (n.data && n.data.type) || ''}</div>
                          </div>
                          <div className="actions">
                            {!n.is_read && <button onClick={() => ackNotification(n.id)} style={{ padding: '6px 8px', borderRadius: 6, background: '#1976d2', color: '#fff', border: 'none', cursor: 'pointer' }}></button>}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
            <button className="menu-toggle" onClick={() => setMenuOpen(o => !o)} aria-haspopup="true" aria-expanded={menuOpen} aria-label=" ">
              <span className="bar" />
              <span className="bar" />
              <span className="bar" />
            </button>
            <div className={`user-dropdown ${menuOpen ? 'open' : ''}`} role="menu">
              {isAdmin && <Link to="/admin" className="user-dropdown-item" role="menuitem" onClick={() => setMenuOpen(false)}></Link>}
              <Link to="/profile" className="user-dropdown-item" role="menuitem" onClick={() => setMenuOpen(false)}></Link>
              <Link to="/wallet" className="user-dropdown-item" role="menuitem" onClick={() => setMenuOpen(false)}></Link>
              <Link to="/wallet?tab=deposit" className="user-dropdown-item" role="menuitem" onClick={() => setMenuOpen(false)}></Link>
              <Link to="/trade" className="user-dropdown-item" role="menuitem" onClick={() => setMenuOpen(false)}></Link>
  
              <button className="user-dropdown-item danger" role="menuitem" onClick={handleLogout}></button>
            </div>
          </div>
        ) : (
          <div className="auth-links">
            <Link to="/login" className="auth-link">
              
            </Link>
            <Link to="/register" className="auth-link register">
              
            </Link>
          </div>
        )}
      </div>
    </nav>
  );
};

const RootLayout = () => {
  const { theme } = useTheme();
  const location = useLocation();
  const showFooter = location.pathname !== '/login' && location.pathname !== '/register';
  return (
    <div className={`app ${theme}`}>
      <header className="header">
        <Navigation />
      </header>
      <main className="main-content">
        <Outlet key={location.pathname + location.search} />
      </main>
      {showFooter && (
        <footer className="footer">
          <div className="footer-content">
            <div className="footer-section">
              <h4>Synergy Trade</h4>
              <p>    </p>
            </div>
            <div className="footer-section">
              <h4></h4>
              <ul>
                <li><Link to="/"></Link></li>
                <li><Link to="/trade"></Link></li>
                <li><Link to="/wallet"></Link></li>
                <li><Link to="/about"> </Link></li>
              </ul>
            </div>
            <div className="footer-section">
              <h4></h4>
              <ul>
                <li><a href="#help"></a></li>
                <li><a href="#contact"></a></li>
                <li><a href="#terms"></a></li>
                <li><a href="#privacy"></a></li>
              </ul>
            </div>
            <div className="footer-section">
              <h4> </h4>
              <div className="social-links">
                <a href="#twitter" className="social-link"></a>
                <a href="#telegram" className="social-link"></a>
                <a href="#discord" className="social-link"></a>
                <a href="#youtube" className="social-link"></a>
              </div>
            </div>
          </div>
          <div className="footer-bottom">
            <p>&copy; 2025 Synergy Group.   .</p>
          </div>
        </footer>
      )}
      <ChatWidget />
    </div>
  );
};

const router = createHashRouter([
  {
    path: '/',
    element: <RootLayout />,
    children: [
      { path: 'activation', element: <ActivationPage /> },
      { path: 'login', element: <LoginPage /> },
      { path: 'register', element: <RegisterPage /> },
      { index: true, element: <HomePage /> },
      { path: 'about', element: <AboutPage /> },
      { path: 'trade', element: <TradePage /> },
      { path: 'broker', element: <BrokerPage /> },
      { path: 'profile', element: <ProfilePage /> },
      { path: 'wallet', element: <WalletPage /> },
      { path: 'admin', element: <AdminPage /> },
      { path: '*', element: <Navigate to="/" replace /> },
    ],
  },
]);

function App() {
  return (
    <ThemeProvider>
      <AuthProvider>
        <AutoTradeProvider>
          <RouterProvider router={router} />
        </AutoTradeProvider>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;
