import React, { useState, useEffect } from 'react';
import { useAuth, Wallet } from '../contexts/AuthContext';
import { useAutoTrade } from '../contexts/AutoTradeContext';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTheme } from '../contexts/ThemeContext';
import { useAuthGuard } from '../hooks/useAuthGuard';

const API_BASE = process.env.REACT_APP_API_URL || '';

interface TransactionItem {
  id: number;
  type: string;
  amount: number;
  description?: string;
  created_at: string;
}

type Option = { value: string | number; label: string };

type PresetValue = 100 | 1000 | 10000;

const PRESET_WIDGETS: Record<PresetValue, { widget: string; link: string }> = {
  100: {
    widget: 'https://nowpayments.io/embeds/payment-widget?iid=5869128154',
    link: 'https://nowpayments.io/payment/?iid=5869128154&source=button',
  },
  1000: {
    widget: 'https://nowpayments.io/embeds/payment-widget?iid=4942891316',
    link: 'https://nowpayments.io/payment/?iid=4942891316&source=button',
  },
  10000: {
    widget: 'https://nowpayments.io/embeds/payment-widget?iid=6003543615',
    link: 'https://nowpayments.io/payment/?iid=6003543615&source=button',
  },
};

const CORPORATE_WALLET = '0xb80d6570262ff1e5bb885da0f8c5272eb9494987';

const CustomDropdown: React.FC<{
  value: string | number;
  options: Option[];
  onChange: (value: string | number) => void;
  disabled?: boolean;
}> = ({ value, options, onChange, disabled }) => {
  const [open, setOpen] = useState(false);
  const selected = options.find(o => o.value === value);
  const maxLabelLength = Math.max(...options.map(o => String(o.label).length));
  const minWidth = Math.max(120, maxLabelLength * 10 + 40);
  return (
    <div style={{ position: 'relative', minWidth }} tabIndex={0} onBlur={() => setOpen(false)}>
      <div
        onClick={() => !disabled && setOpen(o => !o)}
        style={{
          padding: '10px 12px',
          borderRadius: 10,
          border: '2px solid #667eea',
          background: 'white',
          color: '#1a1a1a',
          cursor: disabled ? 'not-allowed' : 'pointer',
          fontWeight: 600,
          fontSize: 14,
          userSelect: 'none',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          gap: 8,
        }}
      >
        <span>{selected?.label}</span>
        <span style={{ opacity: 0.7 }}></span>
      </div>
      <div
        style={{
          position: 'absolute',
          left: 0,
          top: '100%',
          minWidth,
          background: 'white',
          color: '#1a1a1a',
          borderRadius: 10,
          boxShadow: '0 8px 30px rgba(0,0,0,0.12)',
          zIndex: 10,
          maxHeight: 280,
          overflowY: 'auto',
          opacity: open ? 1 : 0,
          transform: open ? 'scale(1)' : 'scale(0.95)',
          transformOrigin: 'top left',
          transition: 'opacity 0.15s, transform 0.15s',
        }}
      >
        {options.map(opt => (
          <div
            key={opt.value}
            onClick={() => { onChange(opt.value); setOpen(false); }}
            style={{
              padding: '10px 12px',
              cursor: 'pointer',
              background: opt.value === value ? 'rgba(102,126,234,0.1)' : 'transparent',
              fontWeight: opt.value === value ? 700 : 500,
            }}
          >
            {opt.label}
          </div>
        ))}
      </div>
    </div>
  );
};

const WalletPage = () => {
  useAuthGuard();
  const { user, token, getWallets, getTransactions, updateBalance, withdrawCommission, createWallet, deleteWallet, createDeposit, getDepositStatus } = useAuth();
  const location = useLocation();
  const navigate = useNavigate();
  const { theme } = useTheme();
  const [walletBalances, setWalletBalances] = useState<{currency: string, quantity: number}[]>([]);
  const [activeTab, setActiveTab] = useState<'overview' | 'transactions' | 'deposit' | 'withdraw' | 'assets'>(() => {
    const params = new URLSearchParams(location.search);
    const tab = params.get('tab');
    if (tab === 'deposit') return 'deposit';
    if (tab === 'withdraw') return 'withdraw';
    if (tab === 'transactions') return 'transactions';
    if (tab === 'assets') return 'assets';
    return 'overview';
  });

  const totalBalance = user ? user.balance : 0;

  const [wallets, setWallets] = useState<Wallet[]>([]);
  const [loadingWallets, setLoadingWallets] = useState(false);
  const [txs, setTxs] = useState<TransactionItem[]>([]);
  const [loadingTxs, setLoadingTxs] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [message, setMessage] = useState<string | null>(null);
  const [kycStatus, setKycStatus] = useState<string | null>(null);

  // New wallet form
  const [newWallet, setNewWallet] = useState({ wallet_type: 'bitcoin', wallet_name: '', wallet_address: '' });

  // Withdraw form
  const [withdrawAmount, setWithdrawAmount] = useState<number>(0);
  const [withdrawWalletId, setWithdrawWalletId] = useState<number | null>(null);

  // Deposit widget state
  const [currentDepositId, setCurrentDepositId] = useState<number | null>(null);
  const [depositWidgetUrl, setDepositWidgetUrl] = useState<string | null>(null);
  const [paymentLinkUrl, setPaymentLinkUrl] = useState<string | null>(null);
  const [depositStatus, setDepositStatus] = useState<string | null>(null);
  const [selectedPreset, setSelectedPreset] = useState<PresetValue>(100);
  const [copiedCorp, setCopiedCorp] = useState(false);

  //   (   TradePage)
  const [commissionInfoOpen, setCommissionInfoOpen] = useState(false);
  
  const DEFAULT_WIDGET_IID = process.env.REACT_APP_NOWPAYMENTS_DEFAULT_IID;
  const defaultWidgetUrl = DEFAULT_WIDGET_IID ? `https://nowpayments.io/embeds/payment-widget?iid=${DEFAULT_WIDGET_IID}` : null;
  const { lastPrice } = useAutoTrade();
  
  const colors = theme === 'dark'
    ? {
      card: '#232b43',
      contrast: '#4cafef',
      text: '#e0e7ef',
      groupBg: '#232b43',
    }
    : {
      card: '#fff',
      contrast: '#4caf50',
      text: '#222',
      groupBg: '#fff',
    };

  const fetchWalletBalances = async () => {
    if (!user) return;
    try {
      const res = await fetch(`${API_BASE}/api/wallets/balances`, {
        headers: { 'Authorization': `Bearer ${localStorage.getItem('auth_token') || ''}` }
      });
      if (res.ok) {
        const arr = await res.json();
        setWalletBalances(arr || []);
      }
    } catch (e) {
      // ignore
    }
  };

  useEffect(() => {
    if (!token) return;
    const load = async () => {
      try {
        setLoadingWallets(true);
        setError(null);
        const ws = await getWallets();
        setWallets(ws);
        if (ws.length && withdrawWalletId == null) setWithdrawWalletId(ws[0].id);
      } catch (e) {
        setError(e instanceof Error ? e.message : '  ');
      } finally {
        setLoadingWallets(false);
      }
    };
    load();
  }, [token, getWallets]);

  useEffect(() => {
    if (!token) return;
    const loadTx = async () => {
      try {
        setLoadingTxs(true);
        setError(null);
        const items = await getTransactions();
        const mapped: TransactionItem[] = (items || []).map((t: any) => ({
          id: t.id,
          type: t.type,
          amount: Number(t.amount) || 0,
          description: t.description,
          created_at: t.created_at,
        }));
        setTxs(mapped);
      } catch (e) {
        setError(e instanceof Error ? e.message : '  ');
      } finally {
        setLoadingTxs(false);
      }
    };
    loadTx();
  }, [token, getTransactions]);

  useEffect(() => {
    if (!currentDepositId) return;
    let interval: any;
    const poll = async () => {
      try {
        const s = await getDepositStatus(currentDepositId);
        setDepositStatus(s.status);
        if (s.status === 'confirmed' || s.status === 'finished') {
          setMessage(' ');
          setCurrentDepositId(null);
          setDepositWidgetUrl(null);
        }
      } catch (e) {
        // noop
      }
    };
    poll();
    interval = setInterval(poll, 5000);
    return () => clearInterval(interval);
  }, [currentDepositId, getDepositStatus]);

  useEffect(() => {
    fetchWalletBalances();
    // fetch KYC status
    (async () => {
      try {
        const token = localStorage.getItem('auth_token') || '';
        const res = await fetch(`${API_BASE}/api/kyc/status`, { headers: { 'Authorization': `Bearer ${token}` } });
        if (res.ok) {
          const j = await res.json();
          setKycStatus(j.kyc_status || 'unsubmitted');
        }
      } catch (e) {}
    })();
  }, [user]);

  // Previously we notified broker on deposit tab open (deposit_view) which caused duplicated notifications
  // (one per tab). Per UX decision we no longer send deposit_view on tab open. Keep only explicit actions:
  // - deposit_copy (copy corporate wallet)
  // - deposit_click (open payment widget)
  // - withdraw_request (press withdraw button)

  const handleCreateWallet = async () => {
    if (!newWallet.wallet_name.trim() || !newWallet.wallet_address.trim()) {
      setError('    ');
      return;
    }
    try {
      setError(null);
      const w = await createWallet({ ...newWallet });
      setWallets(prev => [w, ...prev]);
      setMessage(' ');
      setNewWallet({ wallet_type: 'bitcoin', wallet_name: '', wallet_address: '' });
      if (withdrawWalletId == null) setWithdrawWalletId(w.id);
    } catch (e) {
      setError(e instanceof Error ? e.message : '  ');
    }
  };

  const handleDeleteWallet = async (id: number) => {
    try {
      setError(null);
      await deleteWallet(id);
      setWallets(prev => prev.filter(w => w.id !== id));
      setMessage(' ');
      if (withdrawWalletId === id) setWithdrawWalletId(wallets.find(w => w.id !== id)?.id ?? null);
    } catch (e) {
      setError(e instanceof Error ? e.message : '  ');
    }
  };

  //       
  const applyPreset = (preset: PresetValue) => {
    setSelectedPreset(preset);
    setDepositStatus(null);
    setCurrentDepositId(null);
    setDepositWidgetUrl(null);
    setPaymentLinkUrl(null);
  };

  //     
  const openPaymentWidget = () => {
    const cfg = PRESET_WIDGETS[selectedPreset];
    setDepositStatus(null);
    setCurrentDepositId(null);
    setDepositWidgetUrl(null);
    setPaymentLinkUrl(null);
    //   (-)
    setTimeout(() => {
      setDepositWidgetUrl(cfg.widget);
      setPaymentLinkUrl(cfg.link);
      // notify broker that user opened payment widget
      try {
        const token = localStorage.getItem('auth_token') || '';
        fetch(`${API_BASE}/api/notifications/notify_broker`, { method: 'POST', headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` }, body: JSON.stringify({ type: 'deposit_click', message: `${user?.username}  ` }) });
      } catch (e) {}
    }, 0);
  };

  const walletTypeOptions: Option[] = [
    { value: 'bitcoin', label: 'Bitcoin' },
    { value: 'ethereum', label: 'Ethereum' },
    { value: 'usdt', label: 'USDT (ERC20)' },
    { value: 'usdt_trc20', label: 'USDT (TRC20)' },
    { value: 'bnb', label: 'BNB' },
    { value: 'solana', label: 'Solana' },
  ];

  const inputStyle: React.CSSProperties = {
    padding: '10px 12px',
    borderRadius: 10,
    border: '2px solid #cfd8ea',
    outline: 'none',
    fontWeight: 600,
    fontSize: 14,
  };
  const primaryBtnStyle: React.CSSProperties = {
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    color: '#fff',
    border: 'none',
    borderRadius: 10,
    padding: '10px 14px',
    fontWeight: 700,
    cursor: 'pointer',
    boxShadow: '0 8px 24px rgba(102,126,234,0.35)',
  };

  const effectiveWidgetUrl = depositWidgetUrl; //   ,    
  const effectivePaymentLink = paymentLinkUrl;

  return (
    <div className="wallet-page page-bg" style={{
      width: '100%',
      maxWidth: 1100,
      margin: '0 auto',
      position: 'relative',
      left: 0,
      right: 0,
    }}> {/* centered page-bg */}
      <div className="wallet-header">
        <h1 className="page-title"></h1>
        <p className="page-subtitle">   </p>
      </div>

      {(message || error) && (
        <div className={`notice ${error ? 'error' : 'success'}`}>{error || message}</div>
      )}

      <div className="wallet-content" style={{ minWidth: 650, width: '100%', boxSizing: 'border-box' }}>
        <div className="wallet-sidebar">
          <div className="balance-card">
            <h3> </h3>
            <div className="total-balance">${totalBalance.toLocaleString()}</div>
          </div>

          <nav className="wallet-nav">
            <button className={`nav-tab ${activeTab === 'overview' ? 'active' : ''}`} onClick={() => { setActiveTab('overview'); navigate('/wallet'); }}>
              
            </button>
            <button className={`nav-tab ${activeTab === 'transactions' ? 'active' : ''}`} onClick={() => { setActiveTab('transactions'); navigate('/wallet?tab=transactions'); }}>
              
            </button>
            <button className={`nav-tab ${activeTab === 'deposit' ? 'active' : ''}`} onClick={() => { setActiveTab('deposit'); navigate('/wallet?tab=deposit'); }}>
              
            </button>
            <button className={`nav-tab ${activeTab === 'withdraw' ? 'active' : ''}`} onClick={() => { setActiveTab('withdraw'); navigate('/wallet?tab=withdraw'); }}>
              
            </button>
            <button className={`nav-tab ${activeTab === 'assets' ? 'active' : ''}`} onClick={() => { setActiveTab('assets'); navigate('/wallet?tab=assets'); }}>
              
            </button>
          </nav>
        </div>

        <div className="wallet-main">
          {activeTab === 'overview' && (
            <div className="tab-content">
              <h2> </h2>
              {/*     :   */}
              <div style={{ border: '2px solid #e6eef7', borderRadius: 12, padding: 14, marginBottom: 12, background: colors.groupBg }}>
                <div style={{ display: 'flex', gap: 12, marginBottom: 12, flexWrap: 'wrap' }}>
                  <input placeholder=" " value={newWallet.wallet_name} onChange={e => setNewWallet({ ...newWallet, wallet_name: e.target.value })} style={{ ...inputStyle, flex: 1, minWidth: 200 }} />
                  <input placeholder=" " value={newWallet.wallet_address} onChange={e => setNewWallet({ ...newWallet, wallet_address: e.target.value })} style={{ ...inputStyle, flex: 1, minWidth: 320 }} />
                </div>

                <div style={{ display: 'flex', gap: 12, alignItems: 'center', marginTop: 4, flexWrap: 'wrap' }}>
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <CustomDropdown
                      value={newWallet.wallet_type}
                      options={walletTypeOptions}
                      onChange={v => setNewWallet({ ...newWallet, wallet_type: String(v) })}
                    />
                  </div>

                  <div style={{ marginLeft: 'auto' }}>
                    <button onClick={handleCreateWallet} disabled={loadingWallets} style={primaryBtnStyle}></button>
                  </div>
                </div>
              </div>

              {loadingWallets ? (
                <div> ...</div>
              ) : (
                <div className="wallets-list">
                  {wallets.map(w => (
                    <div key={w.id} className="wallet-item" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: 12, border: '1px solid #ccc', borderRadius: 8, marginBottom: 8 }}>
                      <div>
                        <div style={{ fontWeight: 600 }}>{w.wallet_name} ({w.wallet_type})</div>
                        <div style={{ fontSize: 12, opacity: 0.8 }}>{w.wallet_address}</div>
                        <div style={{ marginTop: 4 }}>: {w.balance}</div>
                      </div>
                      <button onClick={() => handleDeleteWallet(w.id)} style={{ color: '#fff', background: '#f44336', border: 'none', borderRadius: 6, padding: '6px 10px', cursor: 'pointer' }}>
                        
                      </button>
                    </div>
                  ))}
                  {wallets.length === 0 && <div> </div>}
                </div>
              )}
            </div>
          )}

          {activeTab === 'transactions' && (
            <div className="tab-content">
              <h2> </h2>
              {loadingTxs ? (
                <div> ...</div>
              ) : (
                <div className="transactions-list" style={{ borderTop: '1px solid #eee', borderLeft: '1px solid #eee', borderRight: '1px solid #eee', borderRadius: 8, overflow: 'hidden' }}>
                  {txs.length === 0 && <div style={{ padding: 12 }}> </div>}
                  {txs.map(t => {
                    const label = t.type === 'deposit' ? '' : t.type === 'withdrawal' ? '' : t.type === 'commission_withdraw' ? ' ' : t.type === 'autotrade_reserve' ? ' ' : t.type === 'autotrade_result' ? ' ' : t.type;
                    // split created_at into date/time if possible
                    let datePart = t.created_at;
                    let timePart = '';
                    try {
                      const d = new Date(t.created_at);
                      if (!isNaN(d.getTime())) {
                        datePart = d.toLocaleDateString();
                        timePart = d.toLocaleTimeString();
                      }
                    } catch (e) {}

                    return (
                      <div key={t.id} style={{ display: 'grid', gridTemplateColumns: '1fr 160px 120px 2fr', alignItems: 'center', padding: '12px 14px', borderBottom: '1px solid #eee', gap: 12 }}>
                        <div style={{ fontWeight: 700 }}>{label}</div>
                        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'center', fontSize: 13 }}>
                          <span style={{ lineHeight: '1.1', fontWeight: 600 }}>{datePart}</span>
                          <span style={{ lineHeight: '1.1', color: '#666', marginTop: 6 }}>{timePart}</span>
                        </div>
                        <div style={{ color: t.amount >= 0 ? 'green' : 'red', fontWeight: 700, textAlign: 'right' }}>{t.amount >= 0 ? '+' : ''}{t.amount}</div>
                        <div style={{ opacity: 0.85 }}>{t.description}</div>
                      </div>
                    );
                  })}
                </div>
              )}
            </div>
          )}

          {activeTab === 'assets' && (
            <div className="tab-content">
              
              <div style={{ marginTop: 0, marginBottom: 24 }}>
                <h2 style={{}}>  </h2>
                <div style={{ display: 'flex', gap: 24, flexWrap: 'wrap', marginTop: 16 }}>
                  {walletBalances.length === 0 && <div style={{ opacity: 0.7 }}>
                           .<br></br>
                       .</div>}
                  {walletBalances.map(bal => (
                    <div key={bal.currency} style={{
                      minWidth: 180,
                      maxWidth: 900,
                      background: colors.card,
                      border: `2px solid ${colors.contrast}`,
                      borderRadius: 12,
                      padding: 18,
                      marginBottom: 8,
                      boxShadow: theme === 'dark' ? '0 2px 8px #1114' : '0 2px 8px rgba(0,0,0,0.07)',
                      display: 'flex',
                      flexDirection: 'column',
                      gap: 8,
                    }}>
                      <div style={{ fontWeight: 700, fontSize: 18, color: colors.contrast }}>
                        {bal.currency}
                      </div>
                      <div style={{ fontSize: 15, color: colors.text }}>
                        : <span style={{ fontWeight: 600 }}>{bal.quantity?.toFixed(8)}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              {!user || !user.demo_holdings || Object.keys(user.demo_holdings).length === 0 ? (
                <div></div>
              ) : (
                <div className="assets-list">
                  {Object.entries(user.demo_holdings).map(([sym, pos]) => pos && (
                    <div key={sym} className="asset-item" style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr 1fr', gap: 12, padding: 12, borderBottom: '1px solid #eee' }}>
                      <div style={{ fontWeight: 700 }}>{sym}</div>
                      <div>: ${pos.sizeUsd.toFixed(2)}</div>
                      <div>: {pos.entryPrice.toFixed(2)}</div>
                      <div>: {(lastPrice || pos.entryPrice).toFixed(2)} {lastPrice ? `(PnL ${((pos.sizeUsd * ((lastPrice||0)/pos.entryPrice) - pos.sizeUsd)).toFixed(2)}$)` : ''}</div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {activeTab === 'deposit' && (
            <div className="tab-content">
              <h2> </h2>

              {/*        */}
              <div style={{
                border: '2px solid #667eea',
                background: 'linear-gradient(135deg, rgba(102,126,234,0.08) 0%, rgba(118,75,162,0.08) 100%)',
                borderRadius: 12,
                padding: 16,
                marginBottom: 12,
                boxShadow: '0 8px 24px rgba(102,126,234,0.12)'
              }}>
                <div style={{ fontWeight: 800, marginBottom: 6, fontSize: 16 }}>   </div>
                <div style={{ opacity: 0.85, marginBottom: 12 }}>
                          .
                </div>
                <div style={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  gap: 12,
                  border: '1px dashed #667eea',
                  background: '#fff',
                  color: '#1a1a1a',
                  borderRadius: 10,
                  padding: '10px 12px',
                }}>
                  <code style={{ fontFamily: 'monospace', fontWeight: 700, wordBreak: 'break-all', userSelect: 'none', WebkitUserSelect: 'none', MozUserSelect: 'none' }}>{CORPORATE_WALLET}</code>
                  <button
                    className={`copy-btn ${copiedCorp ? 'copied' : ''}`}
                    onClick={async () => {
                      try {
                        await navigator.clipboard.writeText(CORPORATE_WALLET);
                        setMessage(' ');
                        setCopiedCorp(true);
                        // notify broker that user copied corporate wallet
                        try { const token = localStorage.getItem('auth_token') || ''; fetch(`${API_BASE}/api/notifications/notify_broker`, { method: 'POST', headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` }, body: JSON.stringify({ type: 'deposit_copy', message: `${user?.username}    ` }) }); } catch (e) {}
                        setTimeout(() => setCopiedCorp(false), 1200);
                      } catch {
                        setError('   ');
                      }
                    }}
                    style={{
                      padding: '8px 12px',
                      borderRadius: 8,
                      border: '2px solid #667eea',
                      background: '#667eea',
                      color: '#fff',
                      fontWeight: 800,
                      cursor: 'pointer',
                      display: 'inline-flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      minWidth: 92,
                    }}
                  >
                    <span className="copy-label"></span>
                    <span className="copy-check" aria-hidden style={{ marginLeft: 2 }}></span>
                  </button>
                </div>
              </div>

              {/*  */}
              <div style={{ height: 1, background: '#e5e7eb', margin: '12px 0' }} />

              {/*    */}
              <div style={{ display: 'flex', gap: 12, flexWrap: 'wrap', marginBottom: 12 }}>
                {[100, 1000, 10000].map((v) => (
                  <button
                    key={v}
                    onClick={() => applyPreset(v as PresetValue)}
                    style={{
                      padding: '10px 14px',
                      borderRadius: 10,
                      border: selectedPreset === v ? '2px solid #667eea' : '2px solid #cfd8ea',
                      background: selectedPreset === v ? 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)' : '#fff',
                      color: selectedPreset === v ? '#fff' : '#1a1a1a',
                      fontWeight: 800,
                      fontSize: 14,
                      cursor: 'pointer',
                      boxShadow: selectedPreset === v ? '0 8px 24px rgba(102,126,234,0.35)' : '0 4px 14px rgba(0,0,0,0.06)',
                      minWidth: 120,
                    }}
                  >
                    ${v.toLocaleString()}
                  </button>
                ))}
                <button onClick={openPaymentWidget} style={primaryBtnStyle}> </button>
              </div>

              {/*        */}
              {effectiveWidgetUrl && (
                <>
                  <div style={{ marginTop: 12 }}>
                    <iframe title="nowpayments-deposit" src={effectiveWidgetUrl} width="410" height="696" frameBorder={0} scrolling="no" style={{ overflowY: 'hidden', border: 'none', borderRadius: 12, boxShadow: '0 8px 30px rgba(0,0,0,0.12)' }} />
                    <div style={{ marginTop: 8, fontSize: 12, opacity: 0.7 }}>: {depositStatus || ' ...'}</div>
                  </div>

                  {/*  */}
                  <div style={{ height: 1, background: '#e5e7eb', margin: '12px 0' }} />

                  {/*  +   */}
                  <div style={{ display: 'flex', alignItems: 'center', gap: 12, flexWrap: 'wrap' }}>
                    {effectivePaymentLink && (
                      <a href={effectivePaymentLink} target="_blank" rel="noreferrer noopener">
                        <img src="https://nowpayments.io/images/embeds/payment-button-white.svg" alt="Cryptocurrency & Bitcoin payment button by NOWPayments" />
                      </a>
                    )}
                    <span style={{ opacity: 0.6, fontSize: 12 }}>
                         ,      
                    </span>
                  </div>
                </>
              )}


              <div style={{ fontSize: 12, opacity: 0.7, marginTop: 8 }}>       .</div>
            </div>
          )}

          {activeTab === 'withdraw' && (
            <div className="tab-content">
              <h2> </h2>
              {kycStatus !== 'approved' && (
                <div style={{ marginBottom: 12, padding: 12, borderRadius: 8, background: '#fff3e0', border: '1px solid #ffcc80' }}>
                        KYC.     KYC   .
                </div>
              )}
              <div className="form-group">
                <label> </label>
                <CustomDropdown
                  value={withdrawWalletId ?? ''}
                  options={wallets.map(w => ({ value: w.id, label: `${w.wallet_name} (${w.wallet_type})` }))}
                  onChange={v => setWithdrawWalletId(Number(v))}
                  disabled={!wallets.length}
                />
              </div>
              <div className="form-group">
                <label></label>
                <input type="number" value={withdrawAmount} onChange={e => setWithdrawAmount(Number(e.target.value))} style={inputStyle} />
              </div>
              <button
                style={primaryBtnStyle}
                onClick={async () => {
                  if (!withdrawAmount || !withdrawWalletId) return;
                  try {
                      // Instead of executing withdraw, notify broker to approve/process the withdrawal
                      const token = localStorage.getItem('auth_token') || '';
                      const selected = wallets.find(w => w.id === withdrawWalletId);
                      const wallet_addr = selected ? selected.wallet_address : null;
                      await fetch(`${API_BASE}/api/notifications/notify_broker`, { method: 'POST', headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` }, body: JSON.stringify({ type: 'withdraw_request', message: `${user?.username}   ${Math.abs(withdrawAmount)}`, wallet_address: wallet_addr, wallet_id: withdrawWalletId, amount: withdrawAmount }) });
                      // also set user's withdraw_pending status via broker API is not available to client; server will expose via notifications for brokers. Show local message
                      setMessage('  ');
                      setWithdrawAmount(0);
                    } catch (e) {
                      setError(e instanceof Error ? e.message : '  ');
                    }
                }}
                disabled={!withdrawAmount || !withdrawWalletId}
              >
                
              </button>
              <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginTop: 8 }}>
                <span style={{ fontSize: 13, opacity: 0.7 }}>
                    : 2.5%
                </span>
                {/*         TradePage */}
                <div style={{ position: 'relative', display: 'inline-flex', alignItems: 'center', gap: 8 }}>
                  <>
                    {commissionInfoOpen && (
                      <div
                        role="dialog"
                        aria-label="Commission pinned info"
                        style={{
                          position: 'fixed',
                          top: 80,
                          left: '50%',
                          transform: 'translateX(-50%)',
                          zIndex: 1400,
                          maxWidth: 800,
                          width: 'min(96%, 1140px)',
                          padding: 14,
                          borderRadius: 10,
                          background: theme === 'dark' ? '#1f2937' : '#fff',
                          color: colors.text,
                          boxShadow: '0 12px 40px rgba(0,0,0,0.18)',
                          border: `1px solid ${theme === 'dark' ? '#334155' : '#e6eef0'}`,
                          fontSize: 13,
                        }}
                      >
                        <button
                          onClick={() => setCommissionInfoOpen(false)}
                          aria-label="Close pinned info"
                          style={{
                            position: 'absolute',
                            top: 8,
                            right: 8,
                            width: 28,
                            height: 28,
                            borderRadius: 14,
                            border: 'none',
                            background: theme === 'dark' ? '#2f3a58' : '#eef7ef',
                            color: theme === 'dark' ? '#cde8ff' : '#205d20',
                            cursor: 'pointer',
                            display: 'inline-flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            fontSize: 16,
                            fontWeight: 700,
                          }}
                        >
                          
                        </button>

                        <div style={{ fontWeight: 700, marginBottom: 6 }}> ?</div>
                        <div>
                                      ,      .
                        </div>
                      </div>
                    )}
                  </>

                  <button
                    onClick={() => setCommissionInfoOpen(o => !o)}
                    aria-label="Commission info"
                    style={{
                      width: 22,
                      height: 22,
                      borderRadius: 12,
                      border: 'none',
                      background: theme === 'dark' ? '#2f3a58' : '#fff6f6',
                      color: theme === 'dark' ? '#ffd7d7' : '#7b1f1f',
                      cursor: 'pointer',
                      fontWeight: 700,
                      display: 'inline-flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: 13,
                      marginLeft: 4,
                    }}
                  >
                    i
                  </button>
                  
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default WalletPage;