        if (!res.ok) return;
        const j = await res.json();
        const defs = j && j.params ? j.params : null;
        if (!mounted || !defs) return;
        if (typeof defs.L === 'number') setRegenL(defs.L);
        if (typeof defs.w === 'number') setRegenW(defs.w);
        if (typeof defs.muTarget === 'number') setRegenMu(defs.muTarget);
        if (typeof defs.epsilon === 'number') setRegenEps(defs.epsilon);
        if (typeof defs.uMax === 'number') setRegenUMax(defs.uMax);
