import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

export interface Currency {
  symbol: string;
  name: string;
  balance: number;
  value: number;
  change: number;
}

export interface User {
  id: number;
  username: string;
  email: string;
  balance: number;
  broker_tier: string;
  created_at: string;
  phone?: string | null;
  country?: string | null;
  city?: string | null;
  address?: string | null;
  wallet_address?: string | null;
  currencies?: Currency[];
  // demo holdings for manual/auto trading
  demo_holdings?: Record<string, { sizeUsd: number; entryPrice: number } | undefined>;
}

export interface Wallet {
  id: number;
  wallet_type: string;
  wallet_name: string;
  wallet_address: string;
  balance: number;
  created_at: string;
  is_active: boolean;
}

interface AdminSession {
  id: number;
  symbol: string;
  is_running: boolean;
  session_end_ts?: number | null;
  session_start_ts?: number | null;
  reserved_amount?: number | null;
  risk_level?: number | null;
}

interface AdminUserLite {
  id: number;
  username: string;
  email: string;
  phone?: string | null;
  balance: number;
  wallet_address?: string | null;
  autotrade_sessions?: AdminSession[];
  mod_sessions?: AdminSession[];
}

interface AuthContextType {
  user: User | null;
  token: string | null;
  login: (email: string, password: string) => Promise<void>;
  register: (userData: RegisterData) => Promise<void>;
  logout: () => void;
  updateBalance: (amount: number) => Promise<void>;
  updateProfile: (data: Partial<Pick<User, 'username' | 'email' | 'phone' | 'country' | 'city' | 'address' | 'wallet_address'>>) => Promise<void>;
  changePassword: (current_password: string, new_password: string) => Promise<void>;
  getWallets: () => Promise<Wallet[]>;
  createWallet: (input: { wallet_type: string; wallet_name: string; wallet_address: string }) => Promise<Wallet>;
  deleteWallet: (wallet_id: number) => Promise<void>;
  withdrawCommission: (wallet_id: number, amount: number) => Promise<{ new_balance: number }>;
  getTransactions: () => Promise<any[]>;
  createDeposit: (amount: number, currency: string, provider?: string) => Promise<{ deposit_id: number; payment_url: string; widget_url?: string }>;
  getDepositStatus: (deposit_id: number) => Promise<{ id: number; status: string; external_id?: string }>;
  // admin
  isAdmin: boolean;
  adminListUsers: () => Promise<AdminUserLite[]>;
  adminCreditUser: (userId: number, amount: number) => Promise<number>;
  adminDeleteUser: (userId: number) => Promise<void>;
  isLoading: boolean;
  error: string | null;
  // demo holdings helpers
  addDemoHolding: (symbol: string, sizeUsd: number, entryPrice: number) => void;
  closeDemoHolding: (symbol: string) => { sizeUsd: number; entryPrice: number } | null;
}

interface RegisterData {
  username: string;
  email: string;
  password: string;
  broker_code: string;
  wallet_address?: string;
}

interface AuthContextTypeWithProfile extends AuthContextType {
  loadUserProfile: () => Promise<void>;
}
const AuthContext = createContext<AuthContextTypeWithProfile | undefined>(undefined);

const API_BASE_URL = (process.env.REACT_APP_API_URL || '') + '/api';

export const AuthProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [token, setToken] = useState<string | null>(localStorage.getItem('auth_token'));
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const isAdmin = !!user && user.broker_tier === '';

  useEffect(() => {
    // If we have an auth token, try to load the profile automatically.
    // Avoid doing this when the user is currently on the login page
    // (prevents the login button showing a loading state before interaction).
    try {
      const onLoginPath = typeof window !== 'undefined' && window.location && window.location.pathname === '/login';
      if (token && !onLoginPath) {
        loadUserProfile();
      }
    } catch {
      // ignore any errors reading window in non-browser environments
      if (token) loadUserProfile();
    }
  }, [token]);

  const persistUser = (u: User | null) => {
    setUser(u);
    try { if (u) localStorage.setItem('demo_holdings', JSON.stringify(u.demo_holdings || {})); } catch {}
  };

  // Add a request timeout (so a hung network request won't keep auth loading forever)
  const makeAuthenticatedRequest = async (url: string, options: RequestInit = {}, timeoutMs = 10000) => {
    const method = (options.method || 'GET').toUpperCase();
    const hasBody = typeof (options as any).body !== 'undefined';
    const headers: Record<string, string> = {
      ...(options.headers as Record<string, string> || {}),
    };

    if (method !== 'GET' || hasBody) {
      headers['Content-Type'] = headers['Content-Type'] || 'application/json';
    }

    const currentToken = token || localStorage.getItem('auth_token');
    if (currentToken) {
      headers['Authorization'] = `Bearer ${currentToken}`;
    }

    const controller = new AbortController();
    const id = setTimeout(() => controller.abort(), timeoutMs);

    let response: Response;
    try {
      response = await fetch(`${API_BASE_URL}${url}`, {
        ...options,
        headers,
        signal: controller.signal,
      } as any);
    } catch (err) {
      if ((err as any)?.name === 'AbortError') {
        throw new Error('Request timed out');
      }
      throw err;
    } finally {
      clearTimeout(id);
    }

    if (response.status === 401) {
      logout();
      throw new Error(' .  .');
    }

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(errorData.detail || `HTTP ${response.status}`);
    }

    return response.json();
  };

  const loadUserProfile = async () => {
    try {
      setIsLoading(true);
      const userData = await makeAuthenticatedRequest('/profile');
      // merge demo holdings from local storage
      let demo: Record<string, { sizeUsd: number; entryPrice: number } | undefined> = {};
      try { const raw = localStorage.getItem('demo_holdings'); if (raw) demo = JSON.parse(raw); } catch {}
      persistUser({ ...userData, demo_holdings: demo });
      setError(null);
    } catch (err) {
      console.error('  :', err);
      setError(err instanceof Error ? err.message : '  ');
    } finally {
      setIsLoading(false);
    }
  };

  const login = async (email: string, password: string) => {
    try {
      setIsLoading(true);
      setError(null);

      const response = await fetch(`${API_BASE_URL}/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || ' ');
      }

      const data = await response.json();
      const authToken = data.access_token as string;

      // store token immediately so frontend can navigate and request activation if needed
      localStorage.setItem('auth_token', authToken);
      setToken(authToken);

      // If backend indicates activation_required (creator logged in while server locked),
      // do not attempt to fetch profile (which would be blocked). Instead return early and
      // let the caller navigate to the activation UI.
      if (data.activation_required) {
        // leave user as null for now; frontend activation page can call /api/activation/status
        return;
      }

      // Normal flow: fetch profile and persist user
      const profileRes = await fetch(`${API_BASE_URL}/profile`, {
        headers: { 'Authorization': `Bearer ${authToken}` },
      });
      if (!profileRes.ok) {
        const errorData = await profileRes.json().catch(() => ({}));
        throw new Error(errorData.detail || '  ');
      }
      const profile = await profileRes.json();
      let demo: Record<string, { sizeUsd: number; entryPrice: number } | undefined> = {};
      try { const raw = localStorage.getItem('demo_holdings'); if (raw) demo = JSON.parse(raw); } catch {}
      persistUser({ ...profile, demo_holdings: demo });
      setError(null);
    } catch (err) {
      console.error(' :', err);
      setError(err instanceof Error ? err.message : ' ');
      throw err;
    } finally {
      setIsLoading(false);
    }
  };

  const register = async (userData: RegisterData) => {
    try {
      setIsLoading(true);
      setError(null);

      const response = await fetch(`${API_BASE_URL}/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(userData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || ' ');
      }

      await login(userData.email, userData.password);
    } catch (err) {
      console.error(' :', err);
      setError(err instanceof Error ? err.message : ' ');
      throw err;
    } finally {
      setIsLoading(false);
    }
  };

  const logout = () => {
    localStorage.removeItem('auth_token');
    setToken(null);
    setUser(null);
    setError(null);
    try { window.dispatchEvent(new CustomEvent('app-logout')); } catch (e) {}
  };

  const updateBalance = async (amount: number) => {
    try {
      setIsLoading(true);
      const response = await makeAuthenticatedRequest('/balance/update', {
        method: 'POST',
        body: JSON.stringify({ amount }),
      });

      if (user) {
        persistUser({ ...user, balance: response.new_balance });
      }
    } catch (err) {
      console.error('  :', err);
      setError(err instanceof Error ? err.message : '  ');
      throw err;
    } finally {
      setIsLoading(false);
    }
  };

  const updateProfile: AuthContextType['updateProfile'] = async (data) => {
    await makeAuthenticatedRequest('/profile/update', {
      method: 'PUT',
      body: JSON.stringify(data),
    });
    await loadUserProfile();
  };

  const changePassword: AuthContextType['changePassword'] = async (current_password, new_password) => {
    await makeAuthenticatedRequest('/profile/change-password', {
      method: 'POST',
      body: JSON.stringify({ current_password, new_password }),
    });
  };

  const getWallets: AuthContextType['getWallets'] = async () => {
    const data = await makeAuthenticatedRequest('/wallets');
    return data as Wallet[];
  };

  const createWallet: AuthContextType['createWallet'] = async (input) => {
    const data = await makeAuthenticatedRequest('/wallets/create', {
      method: 'POST',
      body: JSON.stringify(input),
    });
    return data as Wallet;
  };

  const deleteWallet: AuthContextType['deleteWallet'] = async (wallet_id) => {
    await makeAuthenticatedRequest(`/wallets/${wallet_id}`, {
      method: 'DELETE',
    });
  };

  const withdrawCommission: AuthContextType['withdrawCommission'] = async (wallet_id, amount) => {
    const data = await makeAuthenticatedRequest('/commission/withdraw', {
      method: 'POST',
      body: JSON.stringify({ wallet_id, amount }),
    });
    if (user && typeof data?.new_balance === 'number') {
      persistUser({ ...user, balance: data.new_balance });
    }
    return { new_balance: data.new_balance };
  };

  const getTransactions: AuthContextType['getTransactions'] = async () => {
    const data = await makeAuthenticatedRequest('/transactions');
    return data as any[];
  };

  const createDeposit: AuthContextType['createDeposit'] = async (amount, currency, provider = 'nowpayments') => {
    const data = await makeAuthenticatedRequest('/deposits/create', {
      method: 'POST',
      body: JSON.stringify({ amount, currency, provider }),
    });
    return data as { deposit_id: number; payment_url: string; widget_url?: string };
  };

  const getDepositStatus: AuthContextType['getDepositStatus'] = async (deposit_id) => {
    const data = await makeAuthenticatedRequest(`/deposits/status/${deposit_id}`);
    return data as { id: number; status: string; external_id?: string };
  };

  // Admin
  const adminListUsers = async () => {
    const data = await makeAuthenticatedRequest('/admin/users');
    // Normalize response: ensure arrays exist
    return (data as AdminUserLite[]).map(u => ({
      ...u,
      autotrade_sessions: Array.isArray(u.autotrade_sessions) ? u.autotrade_sessions : [],
      mod_sessions: Array.isArray(u.mod_sessions) ? u.mod_sessions : [],
    }));
  };

  const adminCreditUser = async (userId: number, amount: number) => {
    const data = await makeAuthenticatedRequest(`/admin/users/${userId}/credit`, {
      method: 'POST',
      body: JSON.stringify({ amount }),
    });
    return data.new_balance as number;
  };

  const adminDeleteUser = async (userId: number) => {
    await makeAuthenticatedRequest(`/admin/users/${userId}`, { method: 'DELETE' });
  };

  // demo holdings helpers
  const addDemoHolding = (symbol: string, sizeUsd: number, entryPrice: number) => {
    if (!user) return;
    const demo = { ...(user.demo_holdings || {}) };
    demo[symbol] = { sizeUsd: (demo[symbol]?.sizeUsd || 0) + sizeUsd, entryPrice };
    const next = { ...user, demo_holdings: demo };
    try { localStorage.setItem('demo_holdings', JSON.stringify(demo)); } catch {}
    setUser(next);
  };
  const closeDemoHolding = (symbol: string) => {
    if (!user) return null;
    const demo = { ...(user.demo_holdings || {}) };
    const pos = demo[symbol] || null;
    delete demo[symbol];
    const next = { ...user, demo_holdings: demo };
    try { localStorage.setItem('demo_holdings', JSON.stringify(demo)); } catch {}
    setUser(next);
    return pos || null;
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        token,
        login,
        register,
        logout,
        updateBalance,
        updateProfile,
        changePassword,
        getWallets,
        createWallet,
        deleteWallet,
        withdrawCommission,
        getTransactions,
        createDeposit,
        getDepositStatus,
        isAdmin,
        adminListUsers,
        adminCreditUser,
        adminDeleteUser,
        isLoading,
        error,
        addDemoHolding,
        closeDemoHolding,
        loadUserProfile,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 