import { useMemo } from 'react';
import { SessionState, Candle } from './types';

// Accept either:
// - modifiedOnly: Candle[] (server returned only modified candles), or
// - wrapped: { candles: Candle[], original_candles?: Candle[] }
// If original_candles is provided, compute per-candle delta = modified.close - original.close
// and set view.high = original.high + delta, view.low = original.low + delta while
// using modified open/close for the body. This preserves wick shape but shifts it by the
// same per-candle delta the server applied.
export function useViewSeries(raw: Candle[] | { candles: Candle[]; original_candles?: Candle[] } | null, session: SessionState | null) {
  return useMemo(() => {
    if (!raw) return [] as Candle[];

    let modified: Candle[] = [];
    let original: Candle[] | null = null;

    if (Array.isArray(raw)) {
      modified = raw as Candle[];
    } else if ((raw as any).candles) {
      modified = (raw as any).candles as Candle[];
      if ((raw as any).original_candles && Array.isArray((raw as any).original_candles)) {
        original = (raw as any).original_candles as Candle[];
      }
    } else {
      return [] as Candle[];
    }

    // If no original provided, return modified as-is but ensure numeric fields
    if (!original) return modified.map((c) => ({ time: c.time, open: Number(c.open), high: Number(c.high), low: Number(c.low), close: Number(c.close), volume: (c as any).volume } as Candle));

    // Compute view series using per-candle delta between modified and original closes.
    const len = Math.min(modified.length, original.length);
    const view: Candle[] = [];
    for (let i = 0; i < len; i++) {
      const m = modified[i];
      const o = original[i];
      const delta = (Number(m.close) - Number(o.close)) || 0;
      const high = Number(o.high) + delta;
      const low = Number(o.low) + delta;
      // initially use modified close; opens will be recomputed below for visual continuity
      view.push({ time: m.time ?? o.time, open: Number(m.open), high, low, close: Number(m.close) } as Candle);
    }

    // If modified has extra candles beyond original length, append them as-is
    if (modified.length > len) {
      for (let i = len; i < modified.length; i++) {
        const c = modified[i];
        view.push({ time: c.time, open: Number(c.open), high: Number(c.high), low: Number(c.low), close: Number(c.close) } as Candle);
      }
    }

    // If session signals an end and a fade window is configured, blend deltas back to original to avoid abrupt cutoffs
    try {
      const w = session && session.params && typeof session.params.w === 'number' ? Math.max(0, Math.floor(session.params.w)) : 0;
      const shouldFade = !!session && (typeof session.endTime !== 'undefined') && w > 0 && original && original.length >= view.length;
      if (shouldFade) {
        const fadeW = Math.min(w, view.length);
        const baseLen = Math.min(view.length, original.length);
        // Blend only the last fadeW candles among the baseLen
        for (let j = 0; j < fadeW; j++) {
          const idx = baseLen - fadeW + j; // target index to blend
          if (idx < 0 || idx >= baseLen) continue;
          const m = modified[idx];
          const o = original[idx];
          const delta = Number(m.close) - Number(o.close);
          const alpha = 1 - (j / Math.max(1, fadeW - 1)); // 1..0
          const blendedClose = Number(o.close) + delta * alpha;
          // adjust high/low similarly by delta*alpha relative to original
          const blendedHigh = Number(o.high) + delta * alpha;
          const blendedLow = Number(o.low) + delta * alpha;
          view[idx].close = blendedClose;
          view[idx].high = blendedHigh;
          view[idx].low = blendedLow;
        }
      }
    } catch (e) {
      // if blending fails, silently continue with unblended view
    }

    // Recompute opens to ensure visual continuity: open = prev.close (or raw/original open for first candle)
    for (let i = 0; i < view.length; i++) {
      if (i === 0) {
        // prefer original open if available else keep existing
        view[i].open = (original && original[0] && typeof original[0].open !== 'undefined') ? Number(original[0].open) : Number(view[i].open);
      } else {
        view[i].open = Number(view[i - 1].close);
      }
    }

    return view;
  }, [raw, session]);
}
