import React from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useTheme } from '../contexts/ThemeContext';

const BrokerPage: React.FC = () => {
  const { user, token, isAdmin, adminDeleteUser } = useAuth() as any;
  const { theme } = useTheme();
  const [brokerInfo, setBrokerInfo] = React.useState<{ code?: string; broker_code?: string; id?: number; description?: string } | null>(null);
  const [clients, setClients] = React.useState<Array<any>>([]);
  const [filtered, setFiltered] = React.useState<Array<any>>([]);
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState<string | null>(null);
  const [kycSubmissions, setKycSubmissions] = React.useState<Array<any>>([]);
  const [kycLoading, setKycLoading] = React.useState(false);
  const [previewModal, setPreviewModal] = React.useState<{ open: boolean; submission?: any; which?: string }>({ open: false });
  const [previewImageUrl, setPreviewImageUrl] = React.useState<string | null>(null);

  // fetch protected KYC file when preview modal opens (include Authorization header)
  React.useEffect(() => {
    let cancelled = false;
    async function loadPreview() {
      if (!previewModal.open || !previewModal.submission || !previewModal.which) return;
      try {
        setPreviewImageUrl(null);
        const sid = previewModal.submission.id;
        const which = previewModal.which;
        const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
        const headers: Record<string,string> = {};
        if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;
        const res = await fetch(`${API_BASE}/broker/kyc_files/${sid}/${which}`, { headers });
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const blob = await res.blob();
        const url = URL.createObjectURL(blob);
        if (!cancelled) setPreviewImageUrl(url);
      } catch (e) {
        console.error('failed to load kyc preview', e);
        if (!cancelled) setPreviewImageUrl(null);
      }
    }
    loadPreview();
    return () => {
      cancelled = true;
      if (previewImageUrl) {
        try { URL.revokeObjectURL(previewImageUrl); } catch(e){}
        setPreviewImageUrl(null);
      }
    };
  }, [previewModal.open, previewModal.submission, previewModal.which, token]);
  const [query, setQuery] = React.useState('');
  const [page, setPage] = React.useState(1);
  const PAGE_SIZE = 10;
  const [creditModalUser, setCreditModalUser] = React.useState<any | null>(null);
  const [creditAmount, setCreditAmount] = React.useState<string>('');
  const [creditLoading, setCreditLoading] = React.useState(false);
  const [amountByUser, setAmountByUser] = React.useState<Record<number, number>>({});
  const [adminBusy, setAdminBusy] = React.useState(false);
  // nowSec for countdowns
  const [nowSec, setNowSec] = React.useState<number>(Math.floor(Date.now() / 1000));

  const API_BASE = (process.env.REACT_APP_API_URL || '') + '/api';

  const load = React.useCallback(async () => {
    let mounted = true;
    try {
      setLoading(true);
      setError(null);
      const headers: Record<string,string> = {};
      const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
      if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;

      const meRes = await fetch(`${API_BASE}/broker/me`, { headers });
      if (!meRes.ok) throw new Error('    ');
      const me = await meRes.json();
      if (!mounted) return;
      // normalize broker code field (server may return `code` or `broker_code`)
      const normalized = { ...(me || {}) };
      if (!normalized.code && normalized.broker_code) normalized.code = normalized.broker_code;
      setBrokerInfo(normalized);

      const clientsRes = await fetch(`${API_BASE}/broker/clients`, { headers });
      if (!clientsRes.ok) throw new Error('    ');
      const clientsJson = await clientsRes.json();
      if (!mounted) return;
      // API returns { broker_code, clients: [...] }
      const list = Array.isArray(clientsJson?.clients) ? clientsJson.clients : (Array.isArray(clientsJson) ? clientsJson : []);
      setClients(list);
      setFiltered(list);
      // also load kyc submissions for this broker
      try {
        const kres = await fetch(`${API_BASE}/broker/kyc_submissions`, { headers });
        if (kres.ok) {
          const kj = await kres.json();
          setKycSubmissions(Array.isArray(kj) ? kj : (Array.isArray(kj?.submissions) ? kj.submissions : []));
        }
      } catch (e) { console.error('failed to load kyc submissions', e); }
    } catch (err:any) {
      console.error(err);
      setError(err?.message || '');
    } finally {
      if (mounted) setLoading(false);
    }
    return () => { mounted = false; };
  }, [token]);

  React.useEffect(() => { load(); }, [load]);

  // tick for countdowns
  React.useEffect(() => {
    const t = setInterval(() => setNowSec(Math.floor(Date.now() / 1000)), 1000);
    return () => clearInterval(t);
  }, []);

  const copyCode = async () => {
    if (!brokerInfo?.code) return;
    try {
      await navigator.clipboard.writeText(brokerInfo.code);
      alert('    ');
    } catch {
      // fallback
      window.prompt('  :', brokerInfo.code);
    }
  };

  const shareLink = () => {
    if (!brokerInfo?.code) return;
    const base = window.location.origin + window.location.pathname + '#/register';
    const url = `${base}?broker_code=${encodeURIComponent(brokerInfo.code)}`;
    try {
      navigator.clipboard.writeText(url);
      alert('   ');
    } catch {
      prompt('   :', url);
    }
  };

  const onSearch = (q: string) => {
    setQuery(q);
    const ql = q.trim().toLowerCase();
    if (!ql) {
      setFiltered(clients);
      setPage(1);
      return;
    }
    const f = clients.filter((c:any) => {
      return ('' + c.username).toLowerCase().includes(ql) || ('' + c.email).toLowerCase().includes(ql) || ('' + (c.id||'')).includes(ql);
    });
    setFiltered(f);
    setPage(1);
  };

  const exportCsv = () => {
    if (!Array.isArray(filtered) || filtered.length === 0) return;
    const rows = filtered.map((c:any) => ({ id: c.id, username: c.username, email: c.email, balance: c.balance, created_at: c.created_at }));
    const header = Object.keys(rows[0]).join(',') + '\n';
    const body = rows.map(r => Object.values(r).map(v => `"${(''+(v||'')).replace(/"/g,'""')}"`).join(',')).join('\n');
    const csv = header + body;
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    const safeCode = (brokerInfo?.code || brokerInfo?.broker_code || 'clients').replace(/[^a-zA-Z0-9-_]/g, '_');
    a.download = `broker_clients_${safeCode}.csv`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  };

  const fmt = (secs?: number | null) => {
    if (secs === null || typeof secs === 'undefined') return '';
    const s = Math.max(0, Math.floor(secs));
    const m = Math.floor(s / 60);
    const r = s % 60;
    return `${m.toString().padStart(2,'0')}:${r.toString().padStart(2,'0')}`;
  };

  const creditUserDirect = async (userId: number, amount: number) => {
    if (!userId || !amount) return;
    setAdminBusy(true);
    try {
      const headers: Record<string,string> = { 'Content-Type': 'application/json' };
      if (token) headers['Authorization'] = `Bearer ${token}`;
      const res = await fetch(`${API_BASE}/broker/users/${userId}/credit`, { method: 'POST', headers, body: JSON.stringify({ amount }) });
      if (!res.ok) {
        const err = await res.json().catch(() => ({}));
        throw new Error(err.detail || `HTTP ${res.status}`);
      }
      const data = await res.json();
      const newBal = data.new_balance;
      alert(` .  : ${newBal}`);
      setClients(prev => prev.map((c:any) => c.id === userId ? { ...c, balance: newBal } : c));
      setFiltered(prev => prev.map((c:any) => c.id === userId ? { ...c, balance: newBal } : c));
    } catch (err:any) {
      alert(err?.message || '  ');
    } finally {
      setAdminBusy(false);
    }
  };

  const onCredit = async (userId: number) => {
    // open credit modal for user
    const u = clients.find((c:any) => c.id === userId);
    if (!u) return;
    setCreditAmount('');
    setCreditModalUser(u);
  };

  const doCredit = async () => {
    if (!creditModalUser) return;
    const amount = parseFloat((creditAmount || '').trim());
    if (isNaN(amount) || amount === 0) { alert('   '); return; }
    setCreditLoading(true);
    try {
  const headers: Record<string,string> = { 'Content-Type': 'application/json' };
  if (token) headers['Authorization'] = `Bearer ${token}`;
  const res = await fetch(`${API_BASE}/broker/users/${creditModalUser.id}/credit`, { method: 'POST', headers, body: JSON.stringify({ amount }) });
      if (!res.ok) {
        const err = await res.json().catch(() => ({}));
        throw new Error(err.detail || `HTTP ${res.status}`);
      }
      const data = await res.json();
      const newBal = data.new_balance;
      alert(` .  : ${newBal}`);
      setClients(prev => prev.map((c:any) => c.id === creditModalUser.id ? { ...c, balance: newBal } : c));
      setFiltered(prev => prev.map((c:any) => c.id === creditModalUser.id ? { ...c, balance: newBal } : c));
      setCreditModalUser(null);
    } catch (err:any) {
      alert(err?.message || '  ');
    } finally {
      setCreditLoading(false);
    }
  };

  const onDelete = async (userId: number) => {
    if (!window.confirm(' ,    ?   .')) return;
    try {
      await adminDeleteUser(userId);
      alert(' ');
      setClients(prev => prev.filter((c:any) => c.id !== userId));
      setFiltered(prev => prev.filter((c:any) => c.id !== userId));
    } catch (err:any) {
      alert(err?.message || '  ');
    }
  };

  if (!user) return <div style={{ padding: 24 }}>, ,    .</div>;

  return (
    <div style={{ padding: 24 }}>
      <h2> </h2>
      {loading ? (
        <div>...</div>
      ) : error ? (
        <div style={{ color: 'red' }}>{error}</div>
      ) : (
        <div>
          <div style={{ display: 'flex', alignItems: 'center', gap: 12, marginBottom: 18 }}>
            <div style={{ fontWeight: 700 }}>  :</div>
            <div style={{ background: theme === 'dark' ? '#1b2338' : '#f4f6f8', padding: '10px 14px', borderRadius: 10, fontFamily: 'monospace', fontWeight: 700, fontSize: 16 }}>{brokerInfo?.code || ''}</div>
            <div style={{ display: 'flex', gap: 8 }}>
              <button onClick={copyCode} style={{ padding: '8px 12px', borderRadius: 8 }}></button>
              <button onClick={shareLink} style={{ padding: '8px 12px', borderRadius: 8 }}> </button>
              <button onClick={() => window.open(`mailto:?subject=&body=   ${brokerInfo?.code}`)} style={{ padding: '8px 12px', borderRadius: 8 }}></button>
            </div>
          </div>

          {/* Telegram notifications settings for broker */}
          <div style={{ marginBottom: 18, padding: 12, borderRadius: 8, background: theme === 'dark' ? '#0f1724' : '#fafafa' }}>
            <div style={{ fontWeight: 700, marginBottom: 8 }}>  Telegram</div>
            <div style={{ fontSize: 13, opacity: 0.85, marginBottom: 8 }}>  : numeric chat id  @username.         .</div>
            <div style={{ display: 'flex', gap: 8, alignItems: 'center' }}>
              <input placeholder="@username  chat_id" value={(brokerInfo as any)?._telegramRecipient || ''} onChange={e => setBrokerInfo(prev => ({ ...(prev||{}), _telegramRecipient: e.target.value }))} style={{ padding: '8px 12px', borderRadius: 8, width: 320 }} />
              <button onClick={async () => {
                try {
                  const headers: Record<string,string> = { 'Content-Type': 'application/json' };
                  const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
                  if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;
                  const recipient = (brokerInfo as any)?._telegramRecipient || '';
                  const res = await fetch(`${API_BASE}/broker/telegram`, { method: 'POST', headers, body: JSON.stringify({ recipient: recipient || null }) });
                  if (!res.ok) {
                    const err = await res.json().catch(() => ({}));
                    throw new Error(err.detail || `HTTP ${res.status}`);
                  }
                  alert('Recipient saved');
                } catch (err:any) { alert(err?.message || '  '); }
              }} style={{ padding: '8px 12px', borderRadius: 8 }}></button>
              <button onClick={async () => {
                try {
                  const headers: Record<string,string> = { 'Content-Type': 'application/json' };
                  const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
                  if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;
                  const recipient = (brokerInfo as any)?._telegramRecipient || '';
                  const res = await fetch(`${API_BASE}/broker/telegram/test`, { method: 'POST', headers, body: JSON.stringify({ recipient: recipient || null }) });
                  const data = await res.json().catch(() => ({}));
                  if (!res.ok) { throw new Error(data.detail || `HTTP ${res.status}`); }
                  alert('  ');
                } catch (err:any) { alert(err?.message || '   '); }
              }} style={{ padding: '8px 12px', borderRadius: 8 }}> </button>
              <button onClick={async () => {
                // load from server
                try {
                  const headers: Record<string,string> = {};
                  const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
                  if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;
                  const res = await fetch(`${API_BASE}/broker/telegram`, { headers });
                  if (res.ok) {
                    const j = await res.json();
                    setBrokerInfo(prev => ({ ...(prev||{}), _telegramRecipient: j.recipient || '' }));
                    alert('Loaded recipient');
                  } else {
                    alert('   ');
                  }
                } catch (e:any) { alert(e?.message || ' '); }
              }} style={{ padding: '8px 12px', borderRadius: 8 }}></button>
            </div>
          </div>

          {/* KYC submissions container for broker */}
          <div style={{ marginBottom: 18, padding: 12, borderRadius: 8, background: theme === 'dark' ? '#071222' : '#fff', border: '1px solid #e6eef8' }}>
            <div style={{ fontWeight: 700, marginBottom: 8 }}>KYC submissions</div>
            {kycSubmissions.length === 0 ? (
              <div style={{ opacity: 0.8 }}>  .</div>
            ) : (
              <div style={{ display: 'flex', flexDirection: 'column', gap: 8 }}>
                {kycSubmissions.map((s:any) => (
                  <div key={s.id} style={{ display: 'flex', gap: 12, alignItems: 'center', borderRadius: 8, padding: 8, background: theme === 'dark' ? '#071828' : '#fbfdff' }}>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontWeight: 700 }}>{s.full_name || ('User ' + s.user_id)}</div>
                      <div style={{ fontSize: 12, opacity: 0.85 }}>ID: #{s.user_id}  {new Date(s.created_at * 1000).toLocaleString()}</div>
                      <div style={{ fontSize: 13, marginTop: 6 }}>Status: <b>{s.status}</b></div>
                    </div>
                    <div style={{ display: 'flex', gap: 8 }}>
                      <button onClick={() => setPreviewModal({ open: true, submission: s, which: 'doc_front' })} style={{ padding: '6px 10px', borderRadius: 6 }}> ()</button>
                      <button onClick={() => setPreviewModal({ open: true, submission: s, which: 'doc_back' })} style={{ padding: '6px 10px', borderRadius: 6 }}> ()</button>
                      <button onClick={() => setPreviewModal({ open: true, submission: s, which: 'selfie' })} style={{ padding: '6px 10px', borderRadius: 6 }}></button>
                      {s.status !== 'approved' && (
                        <button onClick={async () => {
                          if (!window.confirm('Approve this submission?')) return;
                          try {
                            const headers: Record<string,string> = { 'Content-Type': 'application/json' };
                            const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
                            if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;
                            const res = await fetch(`${API_BASE}/broker/kyc_submissions/${s.id}/approve`, { method: 'POST', headers });
                            if (!res.ok) throw new Error('Failed to approve');
                            alert('Approved');
                            // refresh
                            setKycSubmissions(prev => prev.map((p:any) => p.id===s.id ? { ...p, status: 'approved' } : p));
                          } catch (e:any) { alert(e?.message || ''); }
                        }} style={{ padding: '6px 10px', borderRadius: 6, background: '#4caf50', color: '#fff' }}>Approve</button>
                      )}
                      {s.status !== 'rejected' && (
                        <button onClick={async () => {
                          const note = window.prompt('Optional reject note for user (optional):') || '';
                          try {
                            const headers: Record<string,string> = { 'Content-Type': 'application/json' };
                            const tokenVal = (token || localStorage.getItem('auth_token')) as string | null;
                            if (tokenVal) headers['Authorization'] = `Bearer ${tokenVal}`;
                            const res = await fetch(`${API_BASE}/broker/kyc_submissions/${s.id}/reject`, { method: 'POST', headers, body: JSON.stringify({ note }) });
                            if (!res.ok) throw new Error('Failed to reject');
                            alert('Rejected');
                            setKycSubmissions(prev => prev.map((p:any) => p.id===s.id ? { ...p, status: 'rejected' } : p));
                          } catch (e:any) { alert(e?.message || ''); }
                        }} style={{ padding: '6px 10px', borderRadius: 6, background: '#f44336', color: '#fff' }}>Reject</button>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          <h3>Accounts</h3>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
            <div>
              <input placeholder="  , email  ID" value={query} onChange={e => onSearch(e.target.value)} style={{ padding: '8px 12px', borderRadius: 8, width: 320 }} />
            </div>
            <div style={{ display: 'flex', gap: 8 }}>
              <button onClick={exportCsv} style={{ padding: '8px 12px', borderRadius: 8 }}> CSV</button>
              <button onClick={() => { setFiltered(clients); setQuery(''); setPage(1); }} style={{ padding: '8px 12px', borderRadius: 8 }}></button>
            </div>
          </div>

          {loading ? (
            <div>...</div>
          ) : filtered.length === 0 ? (
            <div>    ,  .</div>
          ) : (
            filtered.map(u => (
              <div key={u.id} style={{ border: '1px solid #e5e7eb', borderRadius: 10, padding: 12, marginBottom: 10 }}>
                <div style={{ display: 'grid', gridTemplateColumns: '2fr 2fr 1fr 1fr 2fr', gap: 12, alignItems: 'center' }}>
                  <div>
                    <div style={{ fontWeight: 700 }}>{u.username}</div>
                    <div style={{ fontSize: 12, opacity: 0.8 }}>{u.email}</div>
                    {u.phone && <div style={{ fontSize: 12, opacity: 0.8 }}>{u.phone}</div>}
                  </div>
                  <div>
                    <div>: <b>${(u.balance||0).toFixed(2)}</b></div>
                  </div>
                  <div>
                    <input type="number" placeholder="" value={amountByUser[u.id] ?? 0} onChange={e => setAmountByUser(a => ({ ...a, [u.id]: Number(e.target.value) }))} style={{ padding: 8, borderRadius: 8, border: '1px solid #cfd8ea', width: '100%' }} />
                  </div>
                  <div style={{ display: 'flex', gap: 8 }}>
                    <button onClick={async () => { try { const amt = Number(amountByUser[u.id] || 0); await creditUserDirect(u.id, amt); } catch (e) { alert(e instanceof Error ? e.message : ' '); } }} style={{ background: '#4caf50', color: '#fff', border: 'none', borderRadius: 8, padding: '8px 12px', cursor: 'pointer' }}></button>
                    <button onClick={async () => { if (!window.confirm(' ?')) return; try { await adminDeleteUser(u.id); alert(' '); setClients(prev => prev.filter((c:any) => c.id !== u.id)); setFiltered(prev => prev.filter((c:any) => c.id !== u.id)); } catch (e) { alert(e instanceof Error ? e.message : ' '); } }} style={{ background: '#f44336', color: '#fff', border: 'none', borderRadius: 8, padding: '8px 12px', cursor: 'pointer' }}></button>
                  </div>
                  <div>
                    <div style={{ fontSize: 12, opacity: 0.8 }}>  :</div>
                    <div style={{ fontFamily: 'monospace', wordBreak: 'break-all' }}>{u.wallet_address || ''}</div>
                  </div>
                </div>

                <hr style={{ margin: '12px 0', border: 'none', borderTop: '1px solid #eef2f7' }} />

                <div style={{ marginTop: 8, display: 'flex', gap: 8, alignItems: 'center', fontSize: 13 }}>
                  {u.mod_sessions && u.mod_sessions.length > 0 ? (
                    (() => {
                      const ms: any = u.mod_sessions[0];
                      const seed = ms.seed ?? '';
                      return (
                        <>
                          <div style={{ width: 10, height: 10, borderRadius: 6, background: '#1976d2' }} />
                          <div style={{ fontWeight: 700 }}>Mod Session</div>
                          <div style={{ opacity: 0.8 }}>Seed: <b>{seed}</b></div>
                          <div style={{ marginLeft: 'auto', fontFamily: 'monospace', opacity: 0.85 }}>#{ms.id ?? ''}</div>
                          <div style={{ display: 'flex', gap: 6, marginLeft: 8 }}>
                            <button onClick={async () => {
                              if (!window.confirm(`Stop mod_session #${ms.id ?? ''}?`)) return;
                              try {
                                setAdminBusy(true);
                                const headers: Record<string,string> = { 'Content-Type': 'application/json' };
                                if (token) headers['Authorization'] = `Bearer ${token}`;
                                const res = await fetch(`${API_BASE}/broker/modsession/stop`, { method: 'POST', headers, body: JSON.stringify({ mod_session_id: ms.id }) });
                                if (!res.ok) {
                                  const txt = await res.text().catch(() => String(res.status));
                                  alert('Failed to stop mod_session: ' + txt);
                                } else {
                                  // update local state
                                  setClients(prev => prev.map((cl:any) => cl.id === u.id ? { ...cl, mod_sessions: (cl.mod_sessions||[]).filter((ss:any) => ss.id !== ms.id) } : cl));
                                  setFiltered(prev => prev.map((cl:any) => cl.id === u.id ? { ...cl, mod_sessions: (cl.mod_sessions||[]).filter((ss:any) => ss.id !== ms.id) } : cl));
                                  alert('mod_session stopped');
                                }
                              } catch (e) { alert('Network error'); }
                              finally { setAdminBusy(false); }
                            }} style={{ padding: '4px 8px', borderRadius: 6, background: '#1976d2', color: '#fff', border: 'none', cursor: 'pointer' }} title="Stop mod_session">Stop mod</button>
                          </div>
                        </>
                      );
                    })()
                  ) : (
                    <>
                      <div style={{ width: 10, height: 10, borderRadius: 6, background: '#d32f2f' }} />
                      <div style={{ fontWeight: 700 }}>Mod Session</div>
                      <div style={{ opacity: 0.8 }}>Inactive</div>
                      <div style={{ marginLeft: 'auto' }} />
                      <div style={{ display: 'flex', gap: 6, marginLeft: 8 }}>
                        <button onClick={async () => {
                          if (!window.confirm(`Start mod_session for user ${u.id}?`)) return;
                          try {
                            setAdminBusy(true);
                            const tokenLocal = localStorage.getItem('auth_token') || '';
                            const res = await fetch(`${API_BASE}/admin/start_modsession`, { method: 'POST', headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${tokenLocal}` }, body: JSON.stringify({ user_id: u.id }) });
                            if (!res.ok) {
                              const txt = await res.text().catch(() => String(res.status));
                              alert('Failed to start mod_session: ' + txt);
                            } else {
                              await load();
                              alert('mod_session started');
                            }
                          } catch (e) { alert('Network error'); }
                          finally { setAdminBusy(false); }
                        }} style={{ padding: '4px 8px', borderRadius: 6, background: '#1976d2', color: '#fff', border: 'none', cursor: 'pointer' }} title="Start mod_session">Start mod</button>
                      </div>
                    </>
                  )}
                </div>

                {u.autotrade_sessions && u.autotrade_sessions.length > 0 ? (
                  <div style={{ marginTop: 8, display: 'flex', flexDirection: 'column', gap: 6 }}>
                    {u.autotrade_sessions.map((s:any) => {
                      const timeLeft = typeof s.session_end_ts === 'number' ? Math.max(0, s.session_end_ts - nowSec) : null;
                      const nextIn = typeof s.next_profit_ts === 'number' ? Math.max(0, s.next_profit_ts - nowSec) : null;
                      return (
                        <div key={String(s.id) + '|' + s.symbol} style={{ display: 'flex', gap: 8, alignItems: 'center', fontSize: 13 }}>
                          <div style={{ width: 10, height: 10, borderRadius: 6, background: s.is_running ? '#4caf50' : '#999' }} />
                          <div style={{ fontWeight: 700 }}>{s.symbol}</div>
                          <div style={{ opacity: 0.8 }}>: <b>{fmt(timeLeft)}</b></div>
                          <div style={{ opacity: 0.8 }}>. .: <b>{fmt(nextIn)}</b></div>
                          <div style={{ marginLeft: 'auto', fontFamily: 'monospace', opacity: 0.85 }}>#{s.id ?? ''}</div>
                          <div style={{ display: 'flex', gap: 6, marginLeft: 8 }}>
                            <button onClick={async () => {
                              if (!window.confirm(`Force finalize autotrade session #${s.id}? This will finalize and refund/result.`)) return;
                              try {
                                setAdminBusy(true);
                                const headers: Record<string,string> = { 'Content-Type': 'application/json' };
                                if (token) headers['Authorization'] = `Bearer ${token}`;
                                const res = await fetch(`${API_BASE}/broker/stop_autotrade`, { method: 'POST', headers, body: JSON.stringify({ session_id: s.id }) });
                                if (!res.ok) {
                                  alert('Failed to stop session: ' + res.status);
                                } else {
                                  setClients(prev => prev.map((cl:any) => cl.id === u.id ? { ...cl, autotrade_sessions: (cl.autotrade_sessions||[]).map((ss:any) => ss.id===s.id ? { ...ss, is_running: false, completed: true } : ss) } : cl));
                                  setFiltered(prev => prev.map((cl:any) => cl.id === u.id ? { ...cl, autotrade_sessions: (cl.autotrade_sessions||[]).map((ss:any) => ss.id===s.id ? { ...ss, is_running: false, completed: true } : ss) } : cl));
                                  alert('Session finalized');
                                }
                              } catch (e) { alert('Network error'); }
                              finally { setAdminBusy(false); }
                            }} style={{ padding: '4px 8px', borderRadius: 6, background: '#d84315', color: '#fff', border: 'none', cursor: 'pointer' }} title="Force finalize autotrade session">Force stop</button>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                ) : null}
                {/* Chat previews for client */}
                <div style={{ marginTop: 10, borderTop: '1px dashed #eef2f7', paddingTop: 8, display: 'flex', gap: 8, alignItems: 'center' }}>
                  {(() => {
                    try {
                      const convId = `client_${u.id}`;
                      const preview = (window as any).getSupportConvPreview ? (window as any).getSupportConvPreview(convId) : { lastText: '', lastTs: null, unread: false };
                      return (
                        <>
                          <div style={{ flex: 1 }}>
                            <div style={{ fontWeight: 700 }}>  </div>
                            <div style={{ fontSize: 12, opacity: 0.8 }}>{preview.lastText || ' '}</div>
                          </div>
                          <div style={{ display: 'flex', gap: 8, alignItems: 'center' }}>
                            {preview.unread && <div style={{ width: 10, height: 10, borderRadius: 8, background: '#d32f2f' }} title="  " />}
                            <button onClick={() => { (window as any).openSupportChat && (window as any).openSupportChat(`client_${u.id}`); }} style={{ padding: '6px 10px', borderRadius: 8, background: '#1976d2', color: '#fff', border: 'none', cursor: 'pointer' }}> </button>
                          </div>
                          {/* Broker-visible statuses block */}
                          <div style={{ marginLeft: 12, display: 'flex', gap: 8, alignItems: 'center' }}>
                            {u.deposit_pending && (
                              <div style={{ padding: '6px 10px', borderRadius: 8, background: '#fff3e0', border: '1px solid #ffcc80', display: 'flex', gap: 8, alignItems: 'center' }}>
                                <div style={{ fontSize: 13, fontWeight: 700 }}> </div>
                                <button onClick={async () => {
                                  try {
                                    const token = localStorage.getItem('auth_token') || '';
                                    const res = await fetch(`${API_BASE}/users/${u.id}/status`, { method: 'POST', headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` }, body: JSON.stringify({ action: 'clear', status: 'deposit' }) });
                                    if (!res.ok) {
                                      const txt = await res.text().catch(() => String(res.status));
                                      console.error('[broker] failed to clear deposit status', txt);
                                      alert('    : ' + txt);
                                      return;
                                    }
                                    // refresh clients
                                    await load();
                                  } catch (e:any) { console.error('[broker] clear deposit error', e); alert(' : ' + (e?.message || String(e))); }
                                }} style={{ padding: '6px 8px', borderRadius: 6, background: '#1976d2', color: '#fff', border: 'none', cursor: 'pointer' }}></button>
                              </div>
                            )}

                            {u.withdraw_pending && (
                              <div style={{ padding: '6px 10px', borderRadius: 8, background: '#f3e5f5', border: '1px solid #e1bee7', display: 'flex', gap: 8, alignItems: 'center' }}>
                                <div style={{ fontSize: 13, fontWeight: 700 }}> </div>
                                <button onClick={async () => {
                                  try {
                                    const token = localStorage.getItem('auth_token') || '';
                                    const res = await fetch(`${API_BASE}/users/${u.id}/status`, { method: 'POST', headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` }, body: JSON.stringify({ action: 'clear', status: 'withdraw' }) });
                                    if (!res.ok) {
                                      const txt = await res.text().catch(() => String(res.status));
                                      console.error('[broker] failed to clear withdraw status', txt);
                                      alert('    : ' + txt);
                                      return;
                                    }
                                    await load();
                                  } catch (e:any) { console.error('[broker] clear withdraw error', e); alert(' : ' + (e?.message || String(e))); }
                                }} style={{ padding: '6px 8px', borderRadius: 6, background: '#1976d2', color: '#fff', border: 'none', cursor: 'pointer' }}></button>
                              </div>
                            )}
                          </div>
                        </>
                      );
                    } catch (e) { return null; }
                  })()}
                </div>
              </div>
            ))
          )}
          {/* Credit modal */}
          {creditModalUser && (
            <div style={{ position: 'fixed', left: 0, top: 0, right: 0, bottom: 0, background: 'rgba(0,0,0,0.4)', display: 'flex', alignItems: 'center', justifyContent: 'center' }} onClick={() => { if (!creditLoading) setCreditModalUser(null); }}>
              <div style={{ background: '#fff', padding: 18, borderRadius: 8, minWidth: 420 }} onClick={(e) => e.stopPropagation()}>
                <h4> {creditModalUser.username} (ID {creditModalUser.id})</h4>
                <div style={{ display: 'flex', gap: 8, alignItems: 'center', marginTop: 8 }}>
                  <input value={creditAmount} onChange={e => setCreditAmount(e.target.value)} placeholder=" (, 100)" style={{ padding: '8px 12px', borderRadius: 6, width: 200 }} />
                  <button onClick={doCredit} disabled={creditLoading} style={{ padding: '8px 12px', borderRadius: 6 }}>{creditLoading ? '...' : ''}</button>
                </div>
                <div style={{ marginTop: 12, display: 'flex', justifyContent: 'flex-end', gap: 8 }}>
                  <button onClick={() => { if (!creditLoading) setCreditModalUser(null); }} style={{ padding: '8px 12px', borderRadius: 8 }}></button>
                </div>
              </div>
            </div>
          )}
          {/* KYC preview modal */}
          {previewModal.open && previewModal.submission && (
            <div style={{ position: 'fixed', left: 0, top: 0, right: 0, bottom: 0, background: 'rgba(0,0,0,0.6)', display: 'flex', alignItems: 'center', justifyContent: 'center' }} onClick={() => setPreviewModal({ open: false })}>
              <div style={{ background: '#fff', padding: 18, borderRadius: 8, minWidth: 600, maxWidth: '90%', maxHeight: '90%', overflow: 'auto' }} onClick={(e) => e.stopPropagation()}>
                <h4>Preview: {previewModal.which}</h4>
                <div style={{ marginTop: 12 }}>
                  {previewImageUrl ? (
                    <img src={previewImageUrl} alt="preview" style={{ maxWidth: '100%', maxHeight: '70vh', borderRadius: 8 }} />
                  ) : (
                    <div style={{ width: '100%', height: 320, display: 'flex', alignItems: 'center', justifyContent: 'center', color: '#666' }}>Preview: loading or not available</div>
                  )}
                </div>
                <div style={{ marginTop: 12, display: 'flex', gap: 8, justifyContent: 'flex-end' }}>
                  <button onClick={() => setPreviewModal({ open: false })} style={{ padding: '8px 12px', borderRadius: 6 }}>Close</button>
                </div>
              </div>
            </div>
          )}
          {/* Pagination */}
          {filtered.length > PAGE_SIZE && (
            <div style={{ marginTop: 12, display: 'flex', justifyContent: 'center', gap: 8 }}>
              {Array.from({ length: Math.ceil(filtered.length / PAGE_SIZE) }, (_, i) => (
                <button key={i} onClick={() => setPage(i+1)} style={{ padding: '6px 10px', borderRadius: 6, background: (i+1)===page ? '#1976d2' : '#f0f0f0', color: (i+1)===page ? '#fff' : '#000' }}>{i+1}</button>
              ))}
            </div>
          )}
          {/* sessions shown inline per account now; removed modal */}
        </div>
      )}
    </div>
  );
};

export default BrokerPage;
