import React, { createContext, useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react';
import { useAuth } from './AuthContext';

export type StrategyConfig = {
	bullishEngulfing: boolean;
	bearishEngulfing: boolean;
	maCrossover: boolean;
	breakout: boolean;
};

export type TradeEvent = { id: string; type: 'buy' | 'sell' | 'close'; price: number; time: number };

export type AutoTradeState = {
	enabled: boolean;
	asset: string;
	resolution: number;
	positionSizeUsd: number;
	takeProfitPct: number;
	stopLossPct: number;
	minHoldBars: number;
	reentryCooldownBars: number;
	maxPositions: number;
	strategy: StrategyConfig;
	autoTune: boolean;
	// new behaviour controls
	buyLocalLows: boolean; //    
	lowWindow: number; //   
	waitForProfitOnly: boolean; //    
};

const DEFAULT_STATE: AutoTradeState = {
	enabled: false,
	asset: 'BTCUSD',
	resolution: 150,
	positionSizeUsd: 100,
	takeProfitPct: 1.2,
	stopLossPct: 0.8,
	minHoldBars: 3,
	reentryCooldownBars: 5,
	maxPositions: 1,
	strategy: { bullishEngulfing: true, bearishEngulfing: false, maCrossover: true, breakout: false },
	autoTune: true,
	buyLocalLows: true,
	lowWindow: 20,
	waitForProfitOnly: true,
};

export type AutoTradeContextType = {
	state: AutoTradeState;
	setState: (partial: Partial<AutoTradeState>) => void;
	log: string[];
	trades: TradeEvent[];
	lastPrice: number | null;
	openPosition?: { asset: string; entry: number; sizeUsd: number } | null;
};

const AutoTradeContext = createContext<AutoTradeContextType | undefined>(undefined);

type Candle = { open: number; close: number; high: number; low: number };

function generatePoints(count: number): number[] {
	const pts: number[] = [];
	let price = 100;
	for (let i = 0; i < count; i++) {
		price += (Math.random() - 0.5) * 4;
		pts.push(price);
	}
	return pts;
}
function getCandlesFromPoints(pts: number[]) {
	const c: Candle[] = [];
	for (let i = 1; i < pts.length; i++) {
		const open = pts[i - 1];
		const close = pts[i];
		const high = Math.max(open, close) + Math.random();
		const low = Math.min(open, close) - Math.random();
		c.push({ open, close, high, low });
	}
	return c;
}
function buildModifiedPoints(base: number[], contrast = 1.18) {
	const groupSize = 3;
	const offsets: number[] = Array(Math.ceil(base.length / groupSize)).fill(0).map((_, i) => Math.sin(i / 3) * 1.5);
	const out: number[] = [];
	for (let i = 0; i < base.length; i++) {
		const gi = Math.floor(i / groupSize);
		const nextGi = gi + 1;
		const currOff = offsets[gi] || 0;
		const nextOff = offsets[nextGi] !== undefined ? offsets[nextGi] : currOff;
		const t = (i - gi * groupSize) / groupSize;
		const off = currOff * (1 - t) + nextOff * t;
		if (i === 0) out.push(base[0] + off);
		else out.push(out[i - 1] + (base[i] - base[i - 1]) * contrast + off);
	}
	return out;
}
function sma(values: number[], period: number) {
	if (values.length < period) return null;
	const slice = values.slice(values.length - period);
	return slice.reduce((s, v) => s + v, 0) / period;
}
function stdev(values: number[], period: number) {
	if (values.length < period) return 0;
	const arr = values.slice(values.length - period);
	const mean = arr.reduce((s, v) => s + v, 0) / period;
	const varSum = arr.reduce((s, v) => s + (v - mean) * (v - mean), 0) / period;
	return Math.sqrt(varSum);
}
function atr(candles: Candle[], period = 14) {
	if (candles.length < period + 1) return 0;
	const trs: number[] = [];
	for (let i = candles.length - period; i < candles.length; i++) {
		const prevClose = i > 0 ? candles[i - 1].close : candles[i].open;
		const c = candles[i];
		const tr = Math.max(c.high - c.low, Math.abs(c.high - prevClose), Math.abs(c.low - prevClose));
		trs.push(tr);
	}
	return trs.reduce((s, v) => s + v, 0) / trs.length;
}

export const AutoTradeProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
	const { user, updateBalance } = useAuth();
	const [state, _setState] = useState<AutoTradeState>(() => {
		try {
			const raw = localStorage.getItem('autotrade_state');
			if (raw) return { ...DEFAULT_STATE, ...JSON.parse(raw) } as AutoTradeState;
		} catch {}
		return DEFAULT_STATE;
	});
	const setState = React.useCallback((partial: Partial<AutoTradeState>) => {
		_setState(prev => {
			const next = { ...prev, ...partial } as AutoTradeState;
			localStorage.setItem('autotrade_state', JSON.stringify(next));
			return next;
		});
	}, []);

	const [log, setLog] = useState<string[]>([]);
	const [trades, setTrades] = useState<TradeEvent[]>([]);
	const [lastPrice, setLastPrice] = useState<number | null>(null);
	const [openPosition, setOpenPosition] = useState<{ asset: string; entry: number; sizeUsd: number } | null>(null);

	const timerRef = useRef<any>(null);
	const cooldownRef = useRef<number>(0);
	const prevFlagsRef = useRef<{ buy: boolean; sell: boolean }>({ buy: false, sell: false });
	const streamRef = useRef<{ candles: Candle[] } | null>(null);
	const positionRef = useRef<{ entry: number; sizeUsd: number; peak: number; trailPct: number } | null>(null);
	const barsHeldRef = useRef<number>(0);
	const pendingEntryRef = useRef<{ price: number; expiresIn: number } | null>(null);

	const appendLog = useCallback((msg: string) => setLog(prev => [new Date().toLocaleTimeString() + '  ' + msg, ...prev].slice(0, 400)), []);

	const buildInitialStream = useCallback(() => {
		const base = generatePoints(state.resolution + 2);
		const mod = buildModifiedPoints(base, 1.18);
		return getCandlesFromPoints(mod);
	}, [state.resolution]);

	//    /ATR
	const applyAutoTune = useCallback((candles: Candle[]) => {
		if (!state.autoTune || candles.length < 30) return;
		const closes = candles.map(c => c.close);
		const sigma = Math.abs(stdev(closes.map((v, i, arr) => i === 0 ? 0 : (v - arr[i - 1]) / Math.max(arr[i - 1], 1e-6)), Math.min(30, closes.length)));
		const last = closes[closes.length - 1] || 1;
		const atrVal = atr(candles, 14);
		const atrPct = atrVal / Math.max(last, 1e-6) * 100;
		const slPct = Math.min(3.5, Math.max(0.4, atrPct * 1.2));
		const rr = Math.min(2.5, Math.max(1.4, 1.6 + sigma * 2));
		const tpPct = slPct * rr;
		const minHold = Math.min(8, Math.max(2, Math.round(3 + sigma * 10)));
		const cooldown = Math.min(10, Math.max(2, Math.round(5 + sigma * 15)));
		const targetPos = user ? Math.max(25, Math.min(300, user.balance * Math.min(0.005, 0.02 - sigma * 0.01))) : state.positionSizeUsd;
		const lowWin = Math.min(60, Math.max(10, Math.round(20 + sigma * 30)));
		_setState(prev => {
			const next = { ...prev, stopLossPct: slPct, takeProfitPct: tpPct, minHoldBars: minHold, reentryCooldownBars: cooldown, positionSizeUsd: targetPos, lowWindow: lowWin } as AutoTradeState;
			localStorage.setItem('autotrade_state', JSON.stringify(next));
			return next;
		});
	}, [state.autoTune, user]);

	useEffect(() => {
		if (!state.enabled) {
			if (timerRef.current) clearInterval(timerRef.current);
			timerRef.current = null;
			return;
		}
		streamRef.current = { candles: buildInitialStream() };
		//  
		applyAutoTune(streamRef.current.candles);

		timerRef.current = setInterval(() => {
			const stream = streamRef.current!;
			const candles = stream.candles;
			const last = candles[candles.length - 1];
			const drift = (Math.random() - 0.5) * 2.0;
			const nextClose = Math.max(0.0001, last.close + drift);
			const nextOpen = last.close;
			const nextHigh = Math.max(nextOpen, nextClose) + Math.random();
			const nextLow = Math.min(nextOpen, nextClose) - Math.random();
			candles.push({ open: nextOpen, close: nextClose, high: nextHigh, low: nextLow });
			if (candles.length > state.resolution + 120) candles.shift();
			setLastPrice(nextClose);

			const c1 = candles[candles.length - 2];
			const c2 = candles[candles.length - 1];
			const closes = candles.map(c => c.close);
			const fast = sma(closes, 8);
			const slow = sma(closes, 21);
			const prevFast = sma(closes.slice(0, closes.length - 1), 8);
			const prevSlow = sma(closes.slice(0, closes.length - 1), 21);
			const atrVal = atr(candles, 14);
			const lastPriceVal = c2.close;

			//   ( ~15 )
			if (state.autoTune && Math.floor(Date.now() / 1000) % 15 === 0) applyAutoTune(candles);

			//  
			const engulfBuy = state.strategy.bullishEngulfing && (c1.close < c1.open) && (c2.close > c2.open) && !!slow && c2.close > slow && c2.close >= c1.open && c2.open <= c1.close;
			const crossUp = state.strategy.maCrossover && !!fast && !!slow && !!prevFast && !!prevSlow && prevFast <= prevSlow && fast > slow && (fast - (prevFast || fast)) > 0 && ((slow || 0) - (prevSlow || 0)) >= 0;
			const recentLowN = Math.min(...closes.slice(-Math.max(5, state.lowWindow)));
			const bufAtr = Math.max(0.001, (atrVal / Math.max(lastPriceVal, 1e-6)) * 0.8);
			const lowBuy = state.buyLocalLows && c2.close <= recentLowN * (1 + bufAtr) && c2.close > c2.open;

			const buyFlag = state.buyLocalLows ? lowBuy : (engulfBuy || crossUp);
			const buySignal = buyFlag && !prevFlagsRef.current.buy && c2.close > c1.close;
			prevFlagsRef.current = { buy: buyFlag, sell: false };

			//   / -
			if (positionRef.current) {
				barsHeldRef.current += 1;
				positionRef.current.peak = Math.max(positionRef.current.peak, c2.close);
				// :  trailPct   SL   > SL (     " ")
				if (!state.waitForProfitOnly) {
					const gainPct = ((positionRef.current.peak - positionRef.current.entry) / positionRef.current.entry) * 100;
					if (gainPct > state.stopLossPct) {
						positionRef.current.trailPct = Math.max(positionRef.current.trailPct, state.stopLossPct * 0.6);
					}
				}
			}

			// 
			if (positionRef.current) {
				const p = positionRef.current;
				const changePct = ((c2.close - p.entry) / p.entry) * 100;
				const hitTP = changePct >= state.takeProfitPct;
				if (state.waitForProfitOnly) {
					if (hitTP) {
						const payout = p.sizeUsd * (c2.close / p.entry);
						updateBalance(payout).catch(() => undefined);
						setTrades(prev => [{ id: 't' + Date.now(), type: 'sell' as const, price: c2.close, time: Date.now() }, ...prev].slice(0, 200));
						appendLog(` ${state.asset}  ${c2.close.toFixed(2)} | Payout ${payout.toFixed(2)}$ (PnL ${(payout - p.sizeUsd).toFixed(2)}$)`);
						positionRef.current = null;
						barsHeldRef.current = 0;
						cooldownRef.current = state.reentryCooldownBars;
						pendingEntryRef.current = null;
						setOpenPosition(null);
						return;
					}
				} else {
					//  : TP/SL/
					const trailStop = p.peak * (1 - p.trailPct / 100);
					const stopPrice = Math.min(p.entry * (1 - state.stopLossPct / 100), trailStop || Infinity);
					const hitSL = c2.close <= stopPrice;
					if (hitTP || hitSL) {
						const payout = p.sizeUsd * (c2.close / p.entry);
						updateBalance(payout).catch(() => undefined);
						setTrades(prev => [{ id: 't' + Date.now(), type: 'sell' as const, price: c2.close, time: Date.now() }, ...prev].slice(0, 200));
						appendLog(` ${state.asset}  ${c2.close.toFixed(2)} | Payout ${payout.toFixed(2)}$ (PnL ${(payout - p.sizeUsd).toFixed(2)}$)`);
						positionRef.current = null;
						barsHeldRef.current = 0;
						cooldownRef.current = state.reentryCooldownBars;
						pendingEntryRef.current = null;
						setOpenPosition(null);
						return;
					}
				}
			}

			//  
			if (!positionRef.current) {
				if (cooldownRef.current > 0) { cooldownRef.current -= 1; return; }
				if (buySignal) {
					if (user && user.balance >= state.positionSizeUsd) {
						updateBalance(-state.positionSizeUsd).catch(() => undefined);
						positionRef.current = { entry: c2.close, sizeUsd: state.positionSizeUsd, peak: c2.close, trailPct: 0 };
						barsHeldRef.current = 0;
						setOpenPosition({ asset: state.asset, entry: c2.close, sizeUsd: state.positionSizeUsd });
						setTrades(prev => [{ id: 't' + Date.now(), type: 'buy' as const, price: c2.close, time: Date.now() }, ...prev].slice(0, 200));
						appendLog(` ${state.asset}  ${state.positionSizeUsd}$  ${c2.close.toFixed(2)} ( )`);
					}
					return;
				}

				// :    pullback (,      )
				if (!state.buyLocalLows && slow && atrVal > 0 && (engulfBuy || crossUp)) {
					const atrPctLocal = atrVal / Math.max(lastPriceVal, 1e-6) * 100;
					const k = state.autoTune ? Math.min(0.8, Math.max(0.25, 0.25 + atrPctLocal * 0.04)) : 0.4;
					const ideal = (slow || lastPriceVal) - k * atrVal;
					pendingEntryRef.current = { price: Math.max(ideal, lastPriceVal * (1 - 0.02)), expiresIn: Math.max(5, Math.round(8 + (atrVal / Math.max(lastPriceVal,1e-6)) * 50)) };
					appendLog(`    ${ideal.toFixed(2)} (  ${pendingEntryRef.current.expiresIn} )`);
				}

				if (pendingEntryRef.current) {
					pendingEntryRef.current.expiresIn -= 1;
					if (slow && fast && fast > slow && c2.close <= pendingEntryRef.current.price) {
						if (user && user.balance >= state.positionSizeUsd) {
							updateBalance(-state.positionSizeUsd).catch(() => undefined);
							positionRef.current = { entry: c2.close, sizeUsd: state.positionSizeUsd, peak: c2.close, trailPct: 0 };
							barsHeldRef.current = 0;
							setOpenPosition({ asset: state.asset, entry: c2.close, sizeUsd: state.positionSizeUsd });
							setTrades(prev => [{ id: 't' + Date.now(), type: 'buy' as const, price: c2.close, time: Date.now() }, ...prev].slice(0, 200));
							appendLog(` ${state.asset}  ${state.positionSizeUsd}$  ${c2.close.toFixed(2)} (  )`);
							pendingEntryRef.current = null;
						}
					} else if (pendingEntryRef.current.expiresIn <= 0) {
						appendLog('   ');
						pendingEntryRef.current = null;
					}
				}
			}
		}, 1500);

		return () => { if (timerRef.current) clearInterval(timerRef.current); timerRef.current = null; };
	}, [state.enabled, state.asset, state.resolution, state.positionSizeUsd, state.takeProfitPct, state.stopLossPct, state.minHoldBars, state.reentryCooldownBars, state.maxPositions, state.strategy, state.autoTune, state.buyLocalLows, state.lowWindow, state.waitForProfitOnly, user, updateBalance, buildInitialStream, applyAutoTune, appendLog]);

	const value = useMemo(() => ({ state, setState, log, trades, lastPrice, openPosition }), [state, setState, log, trades, lastPrice, openPosition]);

	return <AutoTradeContext.Provider value={value}>{children}</AutoTradeContext.Provider>;
};

export const useAutoTrade = () => {
	const ctx = useContext(AutoTradeContext);
	if (!ctx) throw new Error('useAutoTrade must be used within AutoTradeProvider');
	return ctx;
}; 