import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useTheme } from '../contexts/ThemeContext';
import { useNavigate, Link } from 'react-router-dom';

const LoginPage = () => {
  const { theme } = useTheme();
  const { login, isLoading, error } = useAuth();
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });
  const [formErrors, setFormErrors] = useState<Record<string, string>>({});

  const colors = theme === 'dark'
    ? {
        bg: 'linear-gradient(135deg, #16213e 0%, #1f2a48 100%)',
        card: '#232b43',
        text: '#e0e7ef',
        input: '#2a3441',
        border: '#4cafef',
        button: '#4cafef',
        buttonHover: '#3a9bd8',
        link: '#4cafef',
        error: '#f44336',
      }
    : {
        bg: 'linear-gradient(135deg, #ffffff 0%, #f0f4f8 100%)',
        card: '#ffffff',
        text: '#1a1a1a',
        input: '#f8fafc',
        border: '#4caf50',
        button: '#4caf50',
        buttonHover: '#45a049',
        link: '#2196f3',
        error: '#f44336',
      };

  const validateForm = () => {
    const errors: Record<string, string> = {};

    if (!formData.email) {
      errors.email = 'Email ';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      errors.email = '  email';
    }

    if (!formData.password) {
      errors.password = ' ';
    }

    setFormErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    try {
      await login(formData.email, formData.password);
      // If login resulted in activation_required the AuthContext.login returns early
      // and user remains null while token is stored  detect that and redirect to activation
      const token = localStorage.getItem('auth_token');
      if (token && (!window || (window && window.location.pathname !== '/activation'))) {
        // If the server is locked and this is the creator, we want to show activation page
        // The AuthContext left user as null intentionally in that case
        // Navigate to activation page; otherwise go to trade
        // A simple heuristic: if user state is null but token exists, prefer activation route
        try {
          // Small delay to allow any background profile fetch to settle
        } catch (e) {}
        navigate('/activation');
        return;
      }
      navigate('/trade');
    } catch (err) {
      //    
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    //     
    if (formErrors[name]) {
      setFormErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  return (
    <div
      style={{
        minHeight: '100vh',
        background: colors.bg,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '20px',
        fontFamily: '"Bahnschrift", Arial, sans-serif',
      }}
    >
      <div
        style={{
          background: colors.card,
          borderRadius: 16,
          padding: '40px',
          boxShadow: theme === 'dark' ? '0 8px 32px #111a' : '0 8px 32px rgba(0,0,0,0.10)',
          width: '100%',
          maxWidth: '400px',
          border: `2px solid ${colors.border}`,
        }}
      >
        <div style={{ textAlign: 'center', marginBottom: '32px' }}>
          <h1
            style={{
              color: colors.text,
              fontSize: '28px',
              fontWeight: 700,
              margin: '0 0 8px 0',
            }}
          >
             
          </h1>
          <p
            style={{
              color: colors.text,
              fontSize: '16px',
              opacity: 0.7,
              margin: 0,
            }}
          >
               
          </p>
        </div>

        {error && (
          <div
            style={{
              background: colors.error,
              color: '#fff',
              padding: '12px 16px',
              borderRadius: 8,
              marginBottom: '20px',
              fontSize: '14px',
              textAlign: 'center',
            }}
          >
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: '20px' }}>
            <label
              style={{
                display: 'block',
                color: colors.text,
                fontSize: '14px',
                fontWeight: 600,
                marginBottom: '8px',
              }}
            >
              Email
            </label>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleInputChange}
              style={{
                width: '100%',
                padding: '12px 16px',
                borderRadius: 8,
                border: formErrors.email ? `2px solid ${colors.error}` : `1px solid ${colors.border}`,
                background: colors.input,
                color: colors.text,
                fontSize: '16px',
                outline: 'none',
                transition: 'border-color 0.2s',
                boxSizing: 'border-box',
              }}
              placeholder="example@mail.com"
            />
            {formErrors.email && (
              <span style={{ color: colors.error, fontSize: '12px', marginTop: '4px', display: 'block' }}>
                {formErrors.email}
              </span>
            )}
          </div>

          <div style={{ marginBottom: '24px' }}>
            <label
              style={{
                display: 'block',
                color: colors.text,
                fontSize: '14px',
                fontWeight: 600,
                marginBottom: '8px',
              }}
            >
              
            </label>
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleInputChange}
              style={{
                width: '100%',
                padding: '12px 16px',
                borderRadius: 8,
                border: formErrors.password ? `2px solid ${colors.error}` : `1px solid ${colors.border}`,
                background: colors.input,
                color: colors.text,
                fontSize: '16px',
                outline: 'none',
                transition: 'border-color 0.2s',
                boxSizing: 'border-box',
              }}
              placeholder=" "
            />
            {formErrors.password && (
              <span style={{ color: colors.error, fontSize: '12px', marginTop: '4px', display: 'block' }}>
                {formErrors.password}
              </span>
            )}
          </div>

          <button
            type="submit"
            disabled={isLoading}
            style={{
              width: '100%',
              padding: '14px',
              borderRadius: 8,
              border: 'none',
              background: isLoading ? colors.border + '80' : colors.button,
              color: '#fff',
              fontSize: '16px',
              fontWeight: 600,
              cursor: isLoading ? 'not-allowed' : 'pointer',
              transition: 'background 0.2s',
              marginBottom: '20px',
            }}
            onMouseEnter={(e) => {
              if (!isLoading) {
                e.currentTarget.style.background = colors.buttonHover;
              }
            }}
            onMouseLeave={(e) => {
              if (!isLoading) {
                e.currentTarget.style.background = colors.button;
              }
            }}
          >
            {isLoading ? '...' : ''}
          </button>
        </form>

        <div style={{ textAlign: 'center' }}>
          <span style={{ color: colors.text, opacity: 0.7, fontSize: '14px' }}>
             ?{' '}
          </span>
          <Link
            to="/register"
            style={{
              color: colors.link,
              textDecoration: 'none',
              fontWeight: 600,
              fontSize: '14px',
            }}
          >
            
          </Link>
        </div>
      </div>
    </div>
  );
};

export default LoginPage; 