import React, { useRef, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

//    (   src/images)
import banner1 from '../images/banner-1.png';
import banner2 from '../images/banner-2.png';
import banner3 from '../images/banner-3.png';
import banner4 from '../images/banner-4.png';
// light theme variants (place files named banner-1l.png, banner-2l.png ...)
import banner1l from '../images/banner-1l.png';
import banner2l from '../images/banner-2l.png';
import banner3l from '../images/banner-3l.png';
import banner4l from '../images/banner-4l.png';

const HomePage = () => {
  const banners = [
    {
      id: 1,
      title: ' ',
      subtitle: '        ,      ,    .                    .       ,      .',
      // primary bluish gradient (site primary colors)
      color: 'linear-gradient(135deg, rgba(102,126,234,0.12), rgba(118,75,162,0.08))',
      imgDark: `url(${banner1})`,
      imgLight: `url(${banner1l})`,
      /* legacy var for backwards compat if needed */
      img: `url(${banner1})`
    },
    {
      id: 2,
      title: ' ',
      subtitle: '           .    ,      ,                  .  ,           ,    ,      .',
      // teal + soft blue blend to match accent / primary tones
      color: 'linear-gradient(135deg, rgba(56,178,172,0.10), rgba(102,126,234,0.06))',
      imgDark: `url(${banner2})`,
      imgLight: `url(${banner2l})`,
      img: `url(${banner2})`
    },
    {
      id: 3,
      title: ' ',
      subtitle: '     .     ,      ,        .      ,       .    ,                 .',
      // subtle purple/blue tint closer to site secondary palette
      color: 'linear-gradient(135deg, rgba(118,75,162,0.08), rgba(102,126,234,0.06))',
      imgDark: `url(${banner3})`,
      imgLight: `url(${banner3l})`,
      img: `url(${banner3})`
    },
    {
      id: 4,
      title: '   ',
      subtitle: ',        .         .         ,             .',
      // cool blue gradient using primary + light accent to match UI
      color: 'linear-gradient(135deg, rgba(76,175,239,0.08), rgba(102,126,234,0.07))',
      imgDark: `url(${banner4})`,
      imgLight: `url(${banner4l})`,
      img: `url(${banner4})`
    }
  ];

  const trackRef = useRef<HTMLDivElement | null>(null);
  const itemsRef = useRef<(HTMLDivElement | null)[]>([]);
  const [current, setCurrent] = useState(0);
  const currentRef = useRef<number>(0);

  useEffect(() => {
    scrollToIndex(0, false);

    const onResize = () => {
      scrollToIndex(currentRef.current, false);
    };
    window.addEventListener('resize', onResize);
    return () => window.removeEventListener('resize', onResize);
  }, []);

  const scrollToIndex = (index: number, smooth = true) => {
    const container = trackRef.current;
    const el = itemsRef.current[index];
    if (!container || !el) return;
    try {
      el.scrollIntoView({
        behavior: smooth ? 'smooth' : 'auto',
        inline: 'center',
        block: 'nearest',
      });
    } catch {
      const containerWidth = container.clientWidth;
      const elWidth = el.clientWidth;
      const targetLeft = el.offsetLeft - (containerWidth - elWidth) / 2;
      container.scrollTo({ left: targetLeft, behavior: smooth ? 'smooth' : 'auto' });
    }
    if (currentRef.current !== index) {
      currentRef.current = index;
      setCurrent(index);
    }
  };

  const prev = () => {
    const nextIndex = Math.max(0, currentRef.current - 1);
    scrollToIndex(nextIndex);
  };
  const next = () => {
    const nextIndex = Math.min(banners.length - 1, currentRef.current + 1);
    scrollToIndex(nextIndex);
  };

  useEffect(() => {
    const container = trackRef.current;
    if (!container) return;
    let raf = 0;
    const onScroll = () => {
      if (raf) cancelAnimationFrame(raf);
      raf = requestAnimationFrame(() => {
        const center = container.scrollLeft + container.clientWidth / 2;
        let closest = 0;
        let minDelta = Infinity;
        itemsRef.current.forEach((el, i) => {
          if (!el) return;
          const elCenter = el.offsetLeft + el.clientWidth / 2;
          const delta = Math.abs(center - elCenter);
          if (delta < minDelta) {
            minDelta = delta;
            closest = i;
          }
        });
        if (currentRef.current !== closest) {
          currentRef.current = closest;
          setCurrent(closest);
        }
      });
    };
    container.addEventListener('scroll', onScroll, { passive: true });
    return () => {
      container.removeEventListener('scroll', onScroll);
      if (raf) cancelAnimationFrame(raf);
    };
  }, []);

  return (
    <div className="home-page page-bg">
      <div className="hero-section">
        <div className="hero-content">
          <h1 className="hero-title">
               <span className="highlight">Synergy Trade</span>
          </h1>
          <p className="hero-subtitle">
                    
          </p>
          <div className="hero-buttons">
            <Link to="/trade" className="btn btn-primary">
               
            </Link>
            <Link to="/about" className="btn btn-secondary">
               
            </Link>
          </div>
        </div>
        <div className="hero-stats">
          <div className="stat-card">
            <div className="stat-number">$0,8B+</div>
            <div className="stat-label"> </div>
          </div>
          <div className="stat-card">
            <div className="stat-number">10K+</div>
            <div className="stat-label"> </div>
          </div>
          <div className="stat-card">
            <div className="stat-number">99.8%</div>
            <div className="stat-label"> </div>
          </div>
        </div>
      </div>

      <section className="banner-section">
        <div className="banner-shell">
          <button className="banner-arrow left" aria-label="Previous" onClick={prev}>
            
          </button>

          <div className="banner-carousel" ref={trackRef}>
            <div className="banner-track">
              {banners.map((b, i) => (
                <div
                  key={b.id}
                  ref={el => { itemsRef.current[i] = el; }}
                  className={`banner-item ${current === i ? 'active' : ''}`}
                  style={
                    {
                      ['--bg-img-dark' as any]: b.imgDark || b.img,
                      ['--bg-img-light' as any]: b.imgLight || b.imgDark || b.img,
                      ['--bg-img' as any]: b.img || b.imgDark, // legacy fallback
                      ['--grad' as any]: b.color
                    } as React.CSSProperties
                  }
                  onClick={() => scrollToIndex(i)}
                  role="button"
                  aria-current={current === i}
                >
                  <div className="banner-content">
                    <h3 className="banner-title">{b.title}</h3>
                    <p className="banner-sub">{b.subtitle}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <button className="banner-arrow right" aria-label="Next" onClick={next}>
            
          </button>
        </div>

        <div className="banner-dots" role="tablist" aria-label=" ">
          {banners.map((_, i) => (
            <button
              key={i}
              className={`dot ${current === i ? 'active' : ''}`}
              onClick={() => scrollToIndex(i)}
              aria-label={` ${i + 1}`}
              aria-pressed={current === i}
            />
          ))}
        </div>
      </section>

      <div className="cta-section">
        <div className="cta-content">
          <h2> ?</h2>
          <p>      !</p>
          <Link to="/trade" className="btn btn-primary btn-large">
             
          </Link>
        </div>
      </div>
    </div>
  );
};

export default HomePage;