// normalize an orderbook entry from various sources (tuple, object with string fields, etc.)
const normalizeOrderbookEntry = (l: any) => {
  if (!l) return { price: null as number | null, amount: 0, rawPrice: null as string | null, rawAmount: null as string | null };
  if (Array.isArray(l)) {
    const rawP = l[0] !== undefined && l[0] !== null ? String(l[0]) : null;
    const rawA = l[1] !== undefined && l[1] !== null ? String(l[1]) : null;
    const p = rawP !== null ? Number(rawP) : NaN;
    const a = rawA !== null ? Number(rawA) : NaN;
    return { price: Number.isFinite(p) ? p : null, amount: Number.isFinite(a) ? a : 0, rawPrice: rawP, rawAmount: rawA };
  }
  const possiblePrice = l.price ?? l.p ?? l[0] ?? null;
  const possibleAmount = l.amount ?? l.qty ?? l[1] ?? l.size ?? null;
  const rawPrice = possiblePrice !== undefined && possiblePrice !== null ? String(possiblePrice) : null;
  const rawAmount = possibleAmount !== undefined && possibleAmount !== null ? String(possibleAmount) : null;
  const pNum = rawPrice !== null ? Number(rawPrice.replace(/,/g, '')) : NaN;
  const aNum = rawAmount !== null ? Number(rawAmount.replace(/,/g, '')) : NaN;
  return { price: Number.isFinite(pNum) ? pNum : null, amount: Number.isFinite(aNum) ? aNum : 0, rawPrice, rawAmount };
};

