const UpdateTimer = ({ timeToUpdate, isUpdating, theme, intervalSeconds = 30 }: { 
  timeToUpdate: number; 
  isUpdating: boolean; 
  theme: string; 
  intervalSeconds?: number;
}) => {
  const radius = 12;
  const strokeWidth = 2;
  const normalizedRadius = radius - strokeWidth * 2;
  const circumference = normalizedRadius * 2 * Math.PI;
  const strokeDasharray = `${circumference} ${circumference}`;
  const safeInterval = Math.max(1, intervalSeconds);
  const progress = (safeInterval - timeToUpdate) / safeInterval;
  const strokeDashoffset = circumference - progress * circumference;
  
  return (
    <div
      style={{
        display: 'flex',
        alignItems: 'center',
        gap: 6,
        padding: '4px 8px',
        borderRadius: 6,
        background: theme === 'dark' ? '#232b43' : '#f6f8fa',
        border: `1px solid ${theme === 'dark' ? '#4cafef' : '#4caf50'}`,
        opacity: isUpdating ? 1 : 0.7,
        transform: isUpdating ? 'scale(1.1)' : 'scale(1)',
        transition: 'all 0.3s ease',
        boxShadow: isUpdating ? `0 0 12px ${theme === 'dark' ? '#4cafef' : '#4caf50'}` : 'none',
      }}
    >
      <svg
        height={radius * 2}
        width={radius * 2}
        style={{
          transform: isUpdating ? 'rotate(270deg)' : `rotate(-90deg)`,
          transition: isUpdating ? 'transform 0.5s ease' : 'transform 0.1s ease',
        }}
      >
        {/*   */}
        <circle
          stroke={theme === 'dark' ? '#4cafef40' : '#4caf5040'}
          fill="transparent"
          strokeWidth={strokeWidth}
          r={normalizedRadius}
          cx={radius}
          cy={radius}
        />
        {/*  */}
        <circle
          stroke={theme === 'dark' ? '#4cafef' : '#4caf50'}
          fill="transparent"
          strokeWidth={strokeWidth}
          strokeDasharray={strokeDasharray}
          strokeDashoffset={isUpdating ? 0 : strokeDashoffset}
          strokeLinecap="round"
          r={normalizedRadius}
          cx={radius}
          cy={radius}
          style={{
            transition: isUpdating ? 'stroke-dashoffset 0.5s ease' : 'stroke-dashoffset 0.1s ease',
          }}
        />
        {/*  */}
        <line
          x1={radius}
          y1={radius}
          x2={radius}
          y2={radius - normalizedRadius * 0.8}
          stroke={theme === 'dark' ? '#fff' : '#222'}
          strokeWidth={1}
          strokeLinecap="round"
          style={{
            transformOrigin: `${radius}px ${radius}px`,
            transform: `rotate(${progress * 360 + 90}deg)`,
            transition: isUpdating ? 'transform 0.5s ease' : 'transform 0.1s ease',
          }}
        />
      </svg>
      <span
        style={{
          fontSize: 11,
          fontWeight: 600,
          color: theme === 'dark' ? '#e0e7ef' : '#222',
          fontFamily: 'monospace',
          minWidth: 16,
          textAlign: 'center',
        }}
      >
        {timeToUpdate}
      </span>
    </div>
  );
};


// ...existing helpers...

function getGroupIdx(i: number) {
  return Math.floor(i / GROUP_SIZE);
}

const getCandlesFromPoints = (pts: number[]) => {
  let candles = [];
  for (let i = 1; i < pts.length; i++) {
    const open = pts[i - 1];
    const close = pts[i];
    const high = Math.max(open, close);
    const low = Math.min(open, close);
    candles.push({
      time: i,
      open,
      close,
      high,
      low,
    });
  }
  return candles;
};

// Small utility: mix two hex colors by weight (w = 0..1 => closer to b)
function mixHex(a: string, b: string, w: number) {
  const clampW = Math.max(0, Math.min(1, w));
  const pa = a.replace('#', '');
  const pb = b.replace('#', '');
  const ar = parseInt(pa.substring(0, 2), 16);
  const ag = parseInt(pa.substring(2, 4), 16);
  const ab = parseInt(pa.substring(4, 6), 16);
  const br = parseInt(pb.substring(0, 2), 16);
  const bg = parseInt(pb.substring(2, 4), 16);
  const bb = parseInt(pb.substring(4, 6), 16);
  const rr = Math.round(ar * (1 - clampW) + br * clampW);
  const rg = Math.round(ag * (1 - clampW) + bg * clampW);
  const rb = Math.round(ab * (1 - clampW) + bb * clampW);
  const toHex = (n: number) => n.toString(16).padStart(2, '0');
  return `#${toHex(rr)}${toHex(rg)}${toHex(rb)}`;
}

