  // animate orderbook price updates over a short duration (ms)
  const animateOrderBookUpdate = (target: OrderBookData, duration = 400) => {
    try {
      const from = orderBookRef.current || { bids: [], asks: [] };
      const to = target || { bids: [], asks: [] };
      const start = performance.now();
      const maxBidLen = Math.max((from.bids || []).length, (to.bids || []).length);
      const maxAskLen = Math.max((from.asks || []).length, (to.asks || []).length);

      const bidsFrom = Array.from({ length: maxBidLen }).map((_, i) => (from.bids && from.bids[i]) ? { price: Number(from.bids[i].price || 0), amount: Number(from.bids[i].amount || 0) } : { price: (to.bids && to.bids[i]) ? Number(to.bids[i].price || 0) : 0, amount: (to.bids && to.bids[i]) ? Number(to.bids[i].amount || 0) : 0 });
      const bidsTo = Array.from({ length: maxBidLen }).map((_, i) => (to.bids && to.bids[i]) ? { price: Number(to.bids[i].price || 0), amount: Number(to.bids[i].amount || 0) } : { price: (from.bids && from.bids[i]) ? Number(from.bids[i].price || 0) : 0, amount: (from.bids && from.bids[i]) ? Number(from.bids[i].amount || 0) : 0 });

      const asksFrom = Array.from({ length: maxAskLen }).map((_, i) => (from.asks && from.asks[i]) ? { price: Number(from.asks[i].price || 0), amount: Number(from.asks[i].amount || 0) } : { price: (to.asks && to.asks[i]) ? Number(to.asks[i].price || 0) : 0, amount: (to.asks && to.asks[i]) ? Number(to.asks[i].amount || 0) : 0 });
      const asksTo = Array.from({ length: maxAskLen }).map((_, i) => (to.asks && to.asks[i]) ? { price: Number(to.asks[i].price || 0), amount: Number(to.asks[i].amount || 0) } : { price: (from.asks && from.asks[i]) ? Number(from.asks[i].price || 0) : 0, amount: (from.asks && from.asks[i]) ? Number(from.asks[i].amount || 0) : 0 });

      let rafId: number | null = null;
      const step = (now: number) => {
        const t = Math.min(1, Math.max(0, (now - start) / Math.max(1, duration)));
        // simple ease-out
        const ease = 1 - Math.pow(1 - t, 3);
        const curBids: OrderBookLevel[] = bidsFrom.map((bf, i) => {
          const bt = bidsTo[i];
          const price = bf.price + (bt.price - bf.price) * ease;
          const amount = bt.amount || bf.amount || 0;
          return { price, amount };
        });
        const curAsks: OrderBookLevel[] = asksFrom.map((af, i) => {
          const at = asksTo[i];
          const price = af.price + (at.price - af.price) * ease;
          const amount = at.amount || af.amount || 0;
          return { price, amount };
        });
        setOrderBook({ bids: curBids, asks: curAsks, lastUpdate: target.lastUpdate || new Date().toLocaleTimeString() });
        if (t < 1) rafId = requestAnimationFrame(step);
        else if (rafId) { cancelAnimationFrame(rafId); rafId = null; }
      };
      rafId = requestAnimationFrame(step);
    } catch (e) {
      // fallback to immediate set
      setOrderBook(target);
    }
  };
  const [chartError, setChartError] = useState<string | null>(null);

