  const getCandleWidth = () => {
    return BASE_CANDLE_WIDTH;
  };

  const candleWidth = getCandleWidth();
  const candleSpacing = candleWidth + 2;

  //     /  user  symbol
  useEffect(() => {
    let mounted = true;
    if (!user) return;
    const token = localStorage.getItem('auth_token') || '';
    (async () => {
      try {
        const res = await fetch(`${API_BASE}/api/autotrade/session?symbol=${encodeURIComponent(symbol)}&user=${user.id}`, {
          headers: { 'Authorization': `Bearer ${token}` }
        });
        if (!mounted) return;
        if (!res.ok) return;
        const data = await res.json();
        // Debug: print server-provided mod generator metadata so we can verify server vs local
        try { console.info('[client] GET /api/autotrade/session => mod_params, mod_seed:', data.mod_params, data.mod_seed); } catch (e) {}
        if (data.is_running) {
          setSessionActive(true);
          //   
          if (data.time_left !== null && data.time_left !== undefined) {
            setTimerLeft(Number(data.time_left));
          } else if (data.session_end_ts) {
            setTimerLeft(Math.max(0, Number(data.session_end_ts) - Math.floor(Date.now() / 1000)));
          }
          // Persist server-provided start/end timestamps for progress calculation
          if (data.session_start_ts) setSessionStartTs(Number(data.session_start_ts));
          if (data.session_end_ts) setSessionEndTs(Number(data.session_end_ts));
          setSessionError(null);
          // If server returned an active cyclic pattern for this session - store it for UI
        } else {
          //          UI
          if (data.completed && data.result_amount != null) {
            setSessionResult(Number(data.result_amount));
            setSessionActive(false);
          }
        }
      } catch (err) {
        console.warn("   :", err);
      }
    })();
    return () => { mounted = false; };
  }, [user, symbol]);

  //  poll   (  )
  useEffect(() => {
    if (!sessionActive || !user) return;
    let mounted = true;
    const token = localStorage.getItem('auth_token') || '';
    const interval = setInterval(async () => {
      try {
        const res = await fetch(`${API_BASE}/api/autotrade/session?symbol=${encodeURIComponent(symbol)}&user=${user.id}`, {
          headers: { 'Authorization': `Bearer ${token}` }
        });
        if (!mounted) return;
        if (!res.ok) return;
        const data = await res.json();
        if (!data.is_running) {
          setSessionActive(false);
          if (data.result_amount != null) {
            setSessionResult(Number(data.result_amount));
            showNotification(` , : +${Number(data.result_amount).toFixed(2)}$`, 'success');
          } else {
            showNotification(` `, 'info');
          }
          // After finishing session  persist endTime, save commit record and
          // allow the client to fade-out over `w` candles before clearing session
          try {
            // compute end timestamp (prefer server-provided session_end_ts in seconds)
            const endTsMs = data.session_end_ts ? Number(data.session_end_ts) * 1000 : Date.now();
            // clear stored start/end timestamps
            setSessionStartTs(null);
            setSessionEndTs(null);

            // If we have an in-memory session, attach endTime and persist it so
            // other tabs will see it and perform their own fade-outs
            // Do not persist mod/session state to localStorage anymore; server is authoritative.
            // Keep session in-memory so UI can fade out.
            try {
              if (session) {
                const sWithEnd = { ...session, endTime: endTsMs };
                // also attempt to fetch a final mod_map snapshot (server may have generated a longer map)
                try {
                  const token = localStorage.getItem('auth_token') || '';
                  const len = rawCandlesArr && rawCandlesArr.length ? rawCandlesArr.length : 0;
                  const mapResp = await fetch(`${API_BASE}/api/modmap?symbol=${encodeURIComponent(symbol)}&timeframe=${encodeURIComponent(intervalValue)}&length=${encodeURIComponent(len)}`, { headers: { 'Authorization': `Bearer ${token}` } });
                  if (mapResp.ok) {
                    const jb = await mapResp.json();
                    if (jb && Array.isArray(jb.map)) {
                      sWithEnd.mod_map = jb.map;
                      sWithEnd.mod_map_generated_at = Date.now();
                    }
                  }
                } catch (e) {}
                setSession(sWithEnd);
              }
            } catch (e) {}

            // Persist a lightweight commit record so UI can indicate modifications persisted
            // Avoid saving committed mod metadata to localStorage; keep lightweight in-memory record only
            const commitMeta = { committedAt: Date.now(), sessionId: session?.id ?? null, seed: session?.seed ?? null, params: session?.params ?? null, endTime: endTsMs };
            try { setCommittedMods(prev => prev ? [...prev, commitMeta] : [commitMeta]); } catch (e) {}

            // Determine fade duration (ms) from session params.w if available
            const fadeCandles = session ? Math.max(1, Math.floor(session.params.w || 3)) : 0;
            const fadeMs = session ? fadeCandles * session.timeframeMs : 0;

            // Schedule clearing of the session and reload after fade-out completes.
            // Add a small buffer (250ms) to ensure rendering completes.
            setTimeout(() => {
              try { clearSession(user ? user.id : undefined); } catch (e) {}
              window.location.reload();
            }, Math.max(0, fadeMs) + 250);
          } catch (e) {}
          return;
        }
        if (data.time_left !== null && data.time_left !== undefined) {
          setTimerLeft(Number(data.time_left));
        } else if (data.session_end_ts) {
          setTimerLeft(Math.max(0, Number(data.session_end_ts) - Math.floor(Date.now() / 1000)));
        }
      } catch (err) {
        // ignore network errors silently
      }
    }, 3000);
    return () => { mounted = false; clearInterval(interval); };
  }, [sessionActive, user, symbol]);

  const startAutoTrade = async () => {
    if (!user) return;
    setSessionError(null);
    setSessionResult(null);
    if (sessionActive) return;
    const token = localStorage.getItem('auth_token') || '';
    try {
      // Request server to start autotrade. Do not send client-generated params/seed  server will generate authoritative metadata.
      const res = await fetch(`${API_BASE}/api/autotrade/session/start`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
        body: JSON.stringify({ symbol, riskLevel: riskLevel, sessionMinutes: sessionMinutes }),
      });
      if (!res.ok) {
        let errMsg = '   ';
        try {
          const body = await res.json();
          errMsg = body.detail || body.message || errMsg;
        } catch {
          errMsg = await res.text().catch(() => errMsg);
        }
        setSessionError(errMsg);
        showNotification(errMsg, 'error');
        return;
      }
      // After successful start: read server response and adopt authoritative modsession metadata in-memory
      try {
        const body = await res.json();
        // Immediately fetch server's authoritative autotrade session metadata and log it so developer can verify server-generated params/seed
        try {
          const infoResp = await fetch(`${API_BASE}/api/autotrade/session?symbol=${encodeURIComponent(symbol)}&user=${user.id}`, { headers: { 'Authorization': `Bearer ${token}` } });
          if (infoResp.ok) {
            const info = await infoResp.json();
            try { console.info('[client] POST /api/autotrade/session/start => server mod metadata:', info.mod_params, info.mod_seed); } catch (e) {}
          }
        } catch (e) {}
        // const serverSessionId = body && (body.mod_session_id || body.session_id) ? Number(body.mod_session_id || body.session_id) : null;
        const s = await ensureCreateUserSession(timeframeMs, user ? user.id : undefined);
      } catch (e) {}
      // reload page to reflect new autotrade state
      window.location.reload();
    } catch (err) {
      setSessionError("   ");
      showNotification("    ", 'error');
    }
  };

  // Build a detailed diagnostics payload and send to server for debugging alignment issues
  const buildDiagnostics = () => {
  const n = rawCandlesArr ? rawCandlesArr.length : 0;
    const diagnostics: any = {
      ts: Date.now(),
      symbol,
      rawCandlesLength: n,
      sessionStartCandleCount,
      sessionActive,
      samples: [],
    };

    // For a number of representative indices, capture mapping details
    const indices: number[] = [];
    // push first, some middles, last, and session start
    if (n > 0) indices.push(0);
    if (sessionStartCandleCount !== null) indices.push(Math.max(0, sessionStartCandleCount - 1));
    for (let i = Math.max(0, n - 10); i < n; i++) indices.push(i);
    // also some midpoints
    for (let i = Math.floor(n / 4); i < Math.floor(n / 4) + 3; i++) indices.push(i);
    for (let i = Math.floor(n / 2); i < Math.floor(n / 2) + 3; i++) indices.push(i);

    const uniq = Array.from(new Set(indices)).filter(i => i >= 0 && i < n);

    uniq.forEach(i => {
      const base = basePoints && basePoints.length > i ? Number(basePoints[i]) : null;
  const raw = rawCandlesArr && rawCandlesArr.length > i ? rawCandlesArr[i] : null;
      const modified = modifiedPoints && modifiedPoints.length > i ? Number(modifiedPoints[i]) : null;
      diagnostics.samples.push({
        idx: i,
        time: raw && raw.time ? raw.time : null,
        basePoint: base,
        rawClose: raw && raw.close ? Number(raw.close) : null,
        modifiedPoint: modified,
      });
    });

    // add computed counters
  diagnostics.sessionCounter = (rawCandlesArr && sessionStartCandleCount !== null) ? Math.max(0, rawCandlesArr.length - sessionStartCandleCount) : null;
    diagnostics.clientComputed = {
  lastIndex: rawCandlesArr ? rawCandlesArr.length - 1 : null,
      lastBase: basePoints && basePoints.length ? basePoints[basePoints.length - 1] : null,
      lastModified: modifiedPoints && modifiedPoints.length ? modifiedPoints[modifiedPoints.length - 1] : null,
    };

    return diagnostics;
  };

