                      if (opt.value === 'crypto') return CRYPTOS.some(c => c.symbol === sym);
                      if (opt.value === 'stocks') return STOCKS.some(c => c.symbol === sym);
                      if (opt.value === 'etf') return ETFS.some(c => c.symbol === sym);
                      if (opt.value === 'forex') return FOREX.some(c => c.symbol === sym);
