import React from 'react';

const API_BASE = process.env.REACT_APP_API_URL || '';

export default function ActivationPage() {
  const [status, setStatus] = React.useState<any>(null);
  const [challenge, setChallenge] = React.useState('');
  const [fingerprint, setFingerprint] = React.useState('');
  const [ownerSecret, setOwnerSecret] = React.useState('');
  const [activationToken, setActivationToken] = React.useState('');
  const [history, setHistory] = React.useState<any[]>([]);
  const [loading, setLoading] = React.useState(false);
  const [message, setMessage] = React.useState('');

  React.useEffect(() => { fetchStatus(); }, []);

  const fetchStatus = async () => {
    try {
      const res = await fetch(`${API_BASE}/api/activation/status`);
      if (!res.ok) throw new Error('failed');
      const j = await res.json();
      setStatus(j);
      setFingerprint(j.fingerprint || '');
    } catch (e) {
      setStatus({ activated: false, fingerprint: '' });
    }
  };

  const requestChallenge = async () => {
    setLoading(true);
    setMessage('');
    try {
      const res = await fetch(`${API_BASE}/api/activation/request`, { method: 'POST' });
      const j = await res.json();
      if (!res.ok) throw new Error(j.detail || 'failed');
      setChallenge(j.challenge || '');
      setFingerprint(j.fingerprint || '');
      setMessage('Challenge created. Use this value when activating (share it with owner out-of-band).');
    } catch (e:any) {
      setMessage(String(e.message || e));
    } finally { setLoading(false); }
  };

  const activate = async () => {
    setLoading(true);
    setMessage('');
    setActivationToken('');
    try {
      const payload = { owner_secret: ownerSecret, challenge };
      const res = await fetch(`${API_BASE}/api/activation/activate`, { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(payload) });
      const j = await res.json();
      if (!res.ok) throw new Error(j.detail || 'failed');
      setActivationToken(j.activation_token || j.activationToken || j.activation_token);
      setMessage('Activated successfully! Keep the activation token safe.');
      fetchStatus();
    } catch (e:any) {
      setMessage(String(e.message || e));
    } finally { setLoading(false); }
  };

  const fetchHistory = async () => {
    if (!ownerSecret) { setMessage('Enter owner secret to view history'); return; }
    setLoading(true); setMessage('');
    try {
      const url = `${API_BASE}/api/activation/history?owner_secret=${encodeURIComponent(ownerSecret)}`;
      const res = await fetch(url);
      const j = await res.json();
      if (!res.ok) throw new Error(j.detail || 'failed');
      setHistory(j || []);
    } catch (e:any) {
      setMessage(String(e.message || e));
    } finally { setLoading(false); }
  };

  return (
    <div style={{ maxWidth: 900, margin: '20px auto', padding: 20 }}>
      <h2>Server Activation (owner-only)</h2>
      <div style={{ padding: 12, border: '1px solid #ddd', borderRadius: 8, marginBottom: 12 }}>
        <div><strong>Activated:</strong> {String(status ? status.activated : false)}</div>
        <div><strong>Fingerprint:</strong> {fingerprint}</div>
      </div>

      <div style={{ display: 'grid', gap: 8 }}>
        <button onClick={requestChallenge} disabled={loading} style={{ padding: '10px 14px' }}>Request Challenge</button>
        <label>Challenge (from request):</label>
        <input value={challenge} onChange={e => setChallenge(e.target.value)} style={{ padding: 8, width: '100%' }} />

        <label>Owner secret:</label>
        <input type="password" value={ownerSecret} onChange={e => setOwnerSecret(e.target.value)} style={{ padding: 8, width: '100%' }} />

        <button onClick={activate} disabled={loading || !ownerSecret || !challenge} style={{ padding: '10px 14px' }}>Activate</button>

        {activationToken && (
          <div style={{ padding: 12, background: '#f6f9ff', borderRadius: 8 }}>
            <div><strong>Activation token (store securely):</strong></div>
            <pre style={{ whiteSpace: 'pre-wrap' }}>{activationToken}</pre>
          </div>
        )}

        <hr />
        <h3>Activation history</h3>
        <div style={{ display: 'flex', gap: 8 }}>
          <button onClick={fetchHistory} disabled={loading || !ownerSecret}>Load history</button>
        </div>
        {message && <div style={{ color: 'crimson', marginTop: 8 }}>{message}</div>}
        <div style={{ marginTop: 12 }}>
          {history.length === 0 && <div>No history loaded</div>}
          {history.map(h => (
            <div key={h.id} style={{ padding: 8, borderBottom: '1px solid #eee' }}>
              <div><strong>{h.created_at}</strong>  {h.success ? 'success' : 'failed'}  {h.detail}</div>
              <div style={{ fontSize: 12, color: '#666' }}>ip: {h.ip} fingerprint: {h.fingerprint}</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
