import { SessionState } from './types';
import { saveSession, loadSession, loadDefaults } from './sessionStorage';

// Use a canonical base timeframe for stored sessions. Mod sessions are always
// computed against this base (1 minute) and then downsampled/scaled for
// longer chart timeframes when rendering.
const BASE_TIMEFRAME_MS = 60 * 1000; // 1 minute

export function initSession(_timeframeMs: number, params?: Partial<SessionState['params']>, opts?: { seed?: number, serverId?: number }): SessionState {
  const now = Date.now();
  const startAligned = Math.floor(now / BASE_TIMEFRAME_MS) * BASE_TIMEFRAME_MS;
  // merge admin-provided defaults (if any)
  const adminDefaults = loadDefaults() || {};

  const session: SessionState = {
  id: (crypto && (crypto as any).randomUUID ? (crypto as any).randomUUID() : 'sess-' + Math.random().toString(36).slice(2, 10)),
  startTime: startAligned,
  // store canonical base timeframe (1 minute)
  timeframeMs: BASE_TIMEFRAME_MS,
    model: 'targetGradientSaw',
  // deterministic numeric seed to allow per-session variation
  seed: typeof opts?.seed === 'number' ? opts!.seed : Math.floor((Math.random() * 0xffffffff)),
    params: {
      L: adminDefaults.L ?? 40,
      w: adminDefaults.w ?? 8,
      muTarget: adminDefaults.muTarget ?? 0.0008,
      epsilon: adminDefaults.epsilon ?? 0.00005,
      gamma: adminDefaults.gamma ?? 0.25,
      delta: adminDefaults.delta ?? 0.0,
      uMax: adminDefaults.uMax ?? 0.06,
      ...(params || {}),
    },
    // optional server session id to tie this client session to server-managed session
    sessionServerId: opts?.serverId,
  };
  return session;
}

export function createUserSession(timeframeMs: number, userId?: number | string, params?: Partial<SessionState['params']>, opts?: { seed?: number, serverId?: number, transientOnly?: boolean }) {
  // Create session with canonical base timeframe and optional server metadata.
  // If opts?.transientOnly is truthy, do NOT persist to localStorage.
  const s = initSession(BASE_TIMEFRAME_MS, params, opts);
  try {
    if (!opts || !opts.transientOnly) {
      // Persist only when explicit non-transient creation is requested
      // callers that want transient sessions should pass { transientOnly: true }
      // Note: saveSession is intentionally best-effort and may throw on quota errors.
      // We avoid persisting generator sessions to local storage by default.
      // eslint-disable-next-line @typescript-eslint/no-var-requires
      const { saveSession } = require('./sessionStorage');
      try { saveSession(s, userId); } catch (e) {}
    }
  } catch (e) {}
  return s;
}

export function getOrCreateSession(timeframeMs: number, userId?: number | string) {
  // load any existing per-user session (sessions are canonical and should
  // apply across timeframes). If none exists, create a new canonical session.
  const existing = loadSession(userId);
  if (existing) return existing;
  const s = initSession(BASE_TIMEFRAME_MS);
  // do not auto-save global session; caller may save per-user
  return s;
}

// Create a transient session (not saved to local storage)  generator should use this to POST to server
export function createTransientSession(params?: Partial<SessionState['params']>, seed?: number) {
  return initSession(BASE_TIMEFRAME_MS, params, { seed, serverId: undefined });
}

export async function ensureCreateUserSession(timeframeMs: number, userId?: number | string) {
  // Create a per-user deterministic session but do NOT persist it to localStorage.
  // This prevents the generator from creating local persistent sessions; server is authoritative.
  try {
    const s = createUserSession(BASE_TIMEFRAME_MS, userId, undefined, { transientOnly: true });
    return s;
  } catch (e) {
    return createUserSession(BASE_TIMEFRAME_MS, userId, undefined, { transientOnly: true });
  }
}
