import { SessionState } from './types';

const KEY = 'chartSession_v1';
const BC_NAME = 'chartSessionChannel_v1';
const DEFAULTS_KEY = 'chartSessionDefaults_v1';

function userKey(userId?: number | string) {
  if (typeof userId === 'undefined' || userId === null) return KEY;
  return `${KEY}_user_${String(userId)}`;
}
export function loadSession(userId?: number | string): SessionState | null {
  try {
    const raw = localStorage.getItem(userKey(userId));
    if (!raw) return null;
    return JSON.parse(raw) as SessionState;
  } catch (e) {
    return null;
  }
}

export function saveSession(s: SessionState, userId?: number | string) {
  try {
    localStorage.setItem(userKey(userId), JSON.stringify(s));
    // broadcast to other tabs
    try {
      const bc = new BroadcastChannel(BC_NAME);
      bc.postMessage({ type: 'session_update', session: s, userId });
      bc.close();
    } catch (e) {
      // BroadcastChannel not available -> ignore
    }
  } catch (e) {
    // ignore quota errors
  }
}

export function saveDefaults(params: any) {
  try { localStorage.setItem(DEFAULTS_KEY, JSON.stringify(params)); } catch (e) {}
}

export function loadDefaults(): any | null {
  try { const raw = localStorage.getItem(DEFAULTS_KEY); if (!raw) return null; return JSON.parse(raw); } catch (e) { return null; }
}

export function onSessionMessage(cb: (s: SessionState, userId?: string | number) => void) {
  try {
    const bc = new BroadcastChannel(BC_NAME);
    bc.onmessage = (ev) => {
      try {
        if (ev.data && ev.data.type === 'session_update' && ev.data.session) {
          cb(ev.data.session as SessionState, ev.data.userId);
        }
      } catch (e) {}
    };
    return () => bc.close();
  } catch (e) {
    return () => {};
  }
}

export function clearSession(userId?: number | string) {
  try { localStorage.removeItem(userKey(userId)); } catch (e) {}
}

export function saveMods(mods: any[], userId?: number | string) {
  try { localStorage.setItem(`${userKey(userId)}_mods`, JSON.stringify(mods || [])); } catch (e) {}
}

export function loadMods(userId?: number | string): any[] | null {
  try { const raw = localStorage.getItem(`${userKey(userId)}_mods`); if (!raw) return null; return JSON.parse(raw); } catch (e) { return null; }
}

export function clearMods(userId?: number | string) {
  try { localStorage.removeItem(`${userKey(userId)}_mods`); } catch (e) {}
}
